.policy {
  position: relative;
  &__head {
    position: relative;
    padding-top: 64px;
    padding-bottom: 40px;
    background: var(--Bg-Light-gray, #f5f5f5);
    @include response(1200px) {
      padding-top: 40px;
    }
  }
  &__head-title {
    position: relative;
    margin: 0 auto 16px;
    padding-bottom: 16px;
    text-align: center;
    font-family: "Instrument Sans";
    font-size: 32px;
    font-style: normal;
    font-weight: 500;
    line-height: 38px;
    border-bottom: 1px solid #d8d8d8;
    max-width: 840px;
    width: 100%;
  }
  &__head-updated {
    position: relative;
    margin-bottom: 16px;
    color: var(--Main-Gray, #595959);
    text-align: center;
    line-height: 20px;
  }
  &__head-text {
    position: relative;
    max-width: 684px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    color: var(--Primary-Black, #000);
    text-align: center;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
  }
  &__head-decor {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
  }
  &__process {
    position: relative;
    padding-top: 80px;
    padding-bottom: 40px;
    @include response(1200px) {
      padding-top: 40px;
      padding-bottom: 20px;
    }
  }
  &__process-box {
    position: relative;
    max-width: 960px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  &__process-title {
    position: relative;
    margin-bottom: 16px;
    color: var(--Primary-Main, #01231f);
    text-align: center;
    font-size: 28px;
    font-weight: 500;
    line-height: 36px;
  }
  &__process-text {
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 24px;
    span {
      font-weight: 500;
    }
    p {
      margin: 0 0 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__process-list {
    position: relative;
    padding-top: 28px;
    padding-bottom: 28px;
    display: flex;
    gap: 48px;
    flex-wrap: wrap;
    @include response(1200px) {
      flex-direction: column;
      align-items: center;
      padding-left: 16px;
      padding-right: 16px;
      row-gap: 32px;
    }
  }
  &__process-item {
    position: relative;
    flex: 0 0 204px;
    width: 204px;
    border-radius: 8px;
    border: 1.5px solid var(--Primary-Accent, #ff3d00);
    background: #fff6f3;
    padding: 20px 16px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    min-height: 91px;
    @include response(1200px) {
      padding: 17px 15px;
      max-width: 350px;
      width: 100%;
      flex: 0 0 auto;
      min-height: auto;
    }
    &:last-child {
      @include response(1200px) {
        .policy__process-mobile-arrow {
          display: none;
        }
      }
    }
    &:nth-last-child(2),
    &:first-child {
      background: $red;
      color: #fff;
    }
    &:nth-child(6),
    &:nth-child(7),
    &:nth-child(8) {
      .policy__process-desktop-arrow {
        transform: scaleX(-1);
        left: auto;
        right: calc(100% - 3px);
      }
    }
    &:nth-child(4) {
      @include response(1200px) {
        &::before {
          content: "";
          position: absolute;
          background: #fff;
          width: 50px;
          height: 10px;
          bottom: -20px;
          left: calc(50% + 1px);
          z-index: 1;
        }
      }
    }
  }
  &__process-main-arrow {
    position: absolute;
    left: -26px;
    bottom: 50%;
    margin-bottom: -4px;
    @include response(1200px) {
      display: none;
    }
  }
  &__process-wrap-arrow {
    position: absolute;
    left: calc(100% - 3px);
    top: 50%;
    margin-top: -3px;
    @include response(1200px) {
      display: none;
    }
  }
  &__process-last-mobile-arrow {
    display: none;
    @include response(1200px) {
      display: block;
      position: absolute;
      bottom: calc(50% - 3px);
      left: -19px;
    }
  }
  &__process-desktop-arrow {
    position: absolute;
    left: calc(100% - 3px);
    top: 50%;
    margin-top: -5px;
    @include response(1200px) {
      display: none;
    }
  }
  &__process-mobile-arrow {
    display: none;
    @include response(1200px) {
      display: block;
      position: absolute;
      left: 50%;
      margin-left: -5px;
      top: calc(100% - 3px);
    }
  }
  &__content {
    position: relative;
    padding-top: 40px;
    padding-bottom: 80px;
    @include response(1200px) {
      padding-top: 20px;
      padding-bottom: 32px;
    }
  }
  &__content-title {
    margin-bottom: 16px;
    color: var(--Primary-Main, #01231f);
    font-size: 28px;
    font-weight: 500;
    line-height: 36px;
  }
  &__content-text {
    font-family: "Instrument Sans";
    font-size: 16px;
    line-height: 24px;
    p {
      margin: 0 0 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    ul {
      font-weight: 500;
      list-style: none;
      padding: 0;
      margin: 0;
      li {
        position: relative;
        padding-left: 30px;
        &::before {
          content: "";
          position: absolute;
          width: 3px;
          height: 3px;
          background: #000;
          top: 10px;
          left: 12px;
        }
      }
    }
  }
  &__team {
    position: relative;
    padding-bottom: 80px;
    @include response(1200px) {
      padding-bottom: 40px;
    }
  }
  &__team-title {
    font-size: 28px;
    font-weight: 500;
    line-height: 36px;
    margin-bottom: 20px;
  }
  &__team-text {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 40px;
    @include response(1200px) {
      margin-bottom: 32px;
    }
  }
  &__team-list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
    @include response(1200px) {
      gap: 16px;
    }
  }
  &__team-item {
    position: relative;
    flex: 0 0 calc(33.333% - 16px);
    width: calc(33.333% - 16px);
    border-radius: 8px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    padding: 40px 32px 24px 32px;
    display: flex;
    flex-direction: column;
    @include response(1200px) {
      padding: 24px;
      flex: 0 0 calc(50% - 8px);
      width: calc(50% - 8px);
    }
    @include response(767px) {
      flex: 0 0 100%;
      width: 100%;
    }
  }
  &__team-item-head {
    position: relative;
    display: flex;
    column-gap: 16px;
    margin-bottom: 8px;
  }
  &__team-item-ava {
    position: relative;
    flex: 0 0 64px;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__team-item-box {
    position: relative;
  }
  &__team-item-title {
    position: relative;
    margin-bottom: 8px;
    font-size: 22px;
    font-weight: 500;
    line-height: 30px;
  }
  &__team-item-head-row {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 12px;
  }
  &__team-item-position {
    color: var(--Secondary-Dark-green, #08584e);
    font-weight: 500;
    line-height: 20px;
    border-radius: 54px;
    background: var(--Bg-Light-green, #ebf8f7);
    padding: 2px 12px;
  }
  &__team-item-social {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    li {
      position: relative;
    }
    a {
      display: flex;
      padding: 6px;
      transition: 0.2s ease-in-out;
      @include desktop-hover() {
        transform: translateY(-2px);
      }
    }
  }
  &__team-item-body {
    position: relative;
    margin-bottom: 24px;
    flex: 1 1 auto;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__team-item-body-inner {
    position: relative;
    font-size: 16px;
    line-height: 24px;
    max-height: 192px;
    overflow: hidden;
    -webkit-line-clamp: 8;
    display: box;
    display: -webkit-box;
    -webkit-box-orient: vertical;
  }
  &__team-item-footer {
    position: relative;
    display: flex;
  }
  &__team-item-link {
    position: relative;
    text-decoration: none;
    display: flex;
    align-items: center;
    column-gap: 8px;
    color: var(--Primary-Black, #000);
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
      svg {
        stroke: $red;
      }
    }
    svg {
      fill: none;
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
  }
  &__write {
    position: relative;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding-top: 80px;
    padding-bottom: 80px;
    @include response(1200px) {
      padding-top: 40px;
      padding-bottom: 40px;
    }
  }
  &__write-box {
    position: relative;
    display: flex;
    column-gap: 20px;
    @include response(1200px) {
      display: block;
    }
  }
  &__write-title {
    font-size: 28px;
    font-weight: 500;
    line-height: 36px;
    flex: 0 0 480px;
    width: 480px;
    @include response(1200px) {
      width: 100%;
      margin-bottom: 16px;
    }
  }
  &__write-content {
    position: relative;
  }
  &__write-text {
    font-size: 16px;
    line-height: 24px;
    p {
      margin: 0 0 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    span {
      color: $red;
      font-weight: 500;
    }
  }
  &__mentions {
    position: relative;
    padding-top: 80px;
    padding-bottom: 80px;
    @include response(1200px) {
      padding-top: 40px;
      padding-bottom: 40px;
    }
  }
  &__mentions-title {
    font-size: 28px;
    font-weight: 500;
    line-height: 36px;
    margin-bottom: 20px;
  }
  &__mentions-text {
    margin-bottom: 40px;
    font-size: 16px;
    line-height: 24px;
    @include response(1200px) {
      margin-bottom: 32px;
    }
  }
  &__feedback {
    position: relative;
    padding-bottom: 40px;
    @include response(1200px) {
      padding-bottom: 20px;
    }
  }
  &__feedback-box {
    position: relative;
    background: var(--Bg-Light-orange, #ffede7);
    padding: 64px;
    display: flex;
    align-items: center;
    column-gap: 20px;
    @include response(1200px) {
      display: block;
      padding: 24px;
    }
  }
  &__feedback-decor {
    position: absolute;
    top: 0;
    height: 100%;
    left: 0;
    @include response(1200px) {
      left: auto;
      right: 0;
    }
  }
  &__feedback-title {
    position: relative;
    font-size: 28px;
    font-weight: 500;
    line-height: 36px;
    flex: 0 0 480px;
    width: 480px;
    @include response(1200px) {
      width: 100%;
      margin-bottom: 16px;
    }
  }
  &__feedback-text {
    position: relative;
    font-size: 16px;
    line-height: 24px;
  }
}
