.journey {
  position: relative;
  padding-top: 80px;
  padding-bottom: 80px;
  max-width: 960px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include response(1200px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
  &__title {
    margin-bottom: 16px;
    color: var(--Primary-Main, #01231f);
    text-align: center;
    font-family: "Instrument Sans";
    font-size: 28px;
    font-style: normal;
    font-weight: 500;
    line-height: 36px;
  }
  &__description {
    color: var(--Primary-Main, #01231f);
    text-align: center;
    font-size: 16px;
    font-style: normal;
    line-height: 24px;
    margin-bottom: 40px;
  }
  &__list {
    position: relative;
    margin-bottom: 40px;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding: 12px;
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 24px;
    margin-bottom: 8px;
    border-radius: 8px;
    padding: 11px 16px 11px 64px;
    border: 1px solid var(--Line-Light, #eaeaea);
    background: var(--Primary-White, #fff);
    @include response(1200px) {
      padding-left: 48px;
      padding-right: 11px;
      display: block;
    }
    &:first-child {
      &::before {
        display: none;
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
    &:nth-last-child(2) {
      &::after {
        background: #ff3d00;
      }
    }
    &::after {
      content: "";
      position: absolute;
      width: 1px;
      background: #0d9a88;
      top: 50%;
      bottom: -9px;
      left: 26.5px;
    }
    &::before {
      content: "";
      position: absolute;
      width: 1px;
      background: #0d9a88;
      top: -1px;
      bottom: 50%;
      left: 26.5px;
    }
    &--current {
      background: #ff3d00;
      border-color: #ff3d00;
      &::before {
        background: #ff3d00;
      }
      &::after {
        display: none;
      }
    }
  }
  &__item-point {
    position: absolute;
    width: 22px;
    height: 22px;
    top: 50%;
    left: 16px;
    margin-top: -11px;
    background: #fff;
    border: 2px solid #fff;
    border-radius: 50%;
    z-index: 2;
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 3px solid #0d9a88;
    }
    .journey__item--current & {
      border-color: #ff3d00;
      background: transparent;
      &::before {
        border-color: #fff;
      }
    }
  }
  &__item-date-box {
    position: relative;
    flex: 0 0 180px;
    width: 180px;
    @include response(1200px) {
      width: 100%;
      margin-bottom: 4px;
    }
  }
  &__item-date {
    position: relative;
    display: inline-flex;
    vertical-align: top;
    align-items: center;
    column-gap: 8px;
    border-radius: 50px;
    background: var(--Bg-Light-green, #ebf8f7);
    padding: 2px 8px;
    color: var(--Primary-Black, #000);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    .journey__item--current & {
      background: rgba(0, 0, 0, 0.2);
      color: #fff;
    }
  }
  &__item-title {
    position: relative;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    .journey__item--current & {
      color: #fff;
    }
  }
  &__articles {
    position: relative;
    color: var(--Primary-Main, #01231f);
    text-align: center;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    margin-bottom: 16px;
  }
  &__articles-item-list {
    position: relative;
    display: flex;
    column-gap: 24px;
    flex-wrap: wrap;
    @include response(1200px) {
      gap: 16px;
      padding-left: 28px;
      padding-right: 28px;
    }
  }
  &__articles-item {
    position: relative;
    @include response(1200px) {
      flex: 0 0 calc(50% - 8px);
      width: calc(50% - 8px);
    }
  }
}
