.types {
  position: relative;
  @include response(1200px) {
    z-index: 2;
    .breadcrumbs {
      display: none;
    }
  }
  &__top {
    position: relative;
    padding-bottom: 40px;
    @include response(1200px) {
      padding-top: 40px;
      padding-bottom: 24px;
    }
  }
  &__back-box {
    display: flex;
    justify-content: center;
    padding-top: 40px;
    padding-bottom: 24px;
    @include response(1200px) {
      padding-top: 0;
    }
  }
  &__back {
    display: flex;
    align-items: center;
    column-gap: 8px;
    text-decoration: none;
    color: var(--Primary-Accent, #ff3d00);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    @include desktop-hover() {
      svg {
        transform: translateX(-3px);
      }
    }
    svg {
      flex: 0 0 auto;
      transition: 0.2s ease-in-out;
    }
  }
  &__title {
    text-align: center;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    column-gap: 12px;
    justify-content: center;
    @include response(1200px) {
      flex-direction: column;
    }
  }
  &__title-img {
    display: flex;
    padding: 8px;
    border-radius: 50%;
    background: var(--Bg-Light-gray, #f5f5f5);
  }
  &__top-text {
    position: relative;
    max-width: 960px;
    width: 100%;
    margin: 0 auto 16px;
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    p {
      margin: 0;
    }
  }
  &__top-updated {
    position: relative;
    margin-bottom: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 8px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__top-updated-icon {
    position: relative;
    flex: 0 0 28px;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    background: var(--Bg-Light-orange, #ffede7);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__top-updated-text {
    color: var(--Main-Gray, #595959);
    font-family: "Helvetica Neue";
    font-size: 16px;
    span {
      font-weight: 700;
    }
  }
  &__top-links {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 12px;
  }
  &__top-link {
    position: relative;
    display: flex;
    align-items: center;
    text-decoration: none;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    column-gap: 12px;
    padding: 8px 16px 8px 8px;
    color: var(--Primary-Black, #000);
    text-align: center;
    font-weight: 500;
    line-height: 20px;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      padding: 8px;
      flex-direction: column;
      row-gap: 8px;
    }
    @include desktop-hover() {
      color: $red;
      svg {
        stroke: $red;
      }
    }
    img {
      flex: 0 0 32px;
      width: 32px;
      height: 32px;
    }
    span {
      flex: 0 0 auto;
    }
    svg {
      flex: 0 0 auto;
      stroke: #000;
      transition: 0.3s ease-in-out;
      @include response(1200px) {
        display: none;
      }
    }
  }
  &__filters-box {
    position: relative;
    padding-top: 32px;
    padding-bottom: 32px;
    background: var(--Bg-Light-gray, #f5f5f5);
    @include response(1200px) {
      padding-top: 16px;
      padding-bottom: 16px;
      border-top: 1px solid var(--Line-Light, #eaeaea);
      border-bottom: 1px solid var(--Line-Light, #eaeaea);
      // position: sticky;
      // top: 0;
      z-index: 1;
    }
  }
  &__table {
    position: relative;
    padding-bottom: 20px;
  }
  &__table-head {
    position: relative;
    display: flex;
    align-items: center;
    padding: 32px 0 20px;
    column-gap: 20px;
    justify-content: space-between;
    @include response(1200px) {
      padding-top: 16px;
      padding-bottom: 16px;
      flex-wrap: wrap;
      row-gap: 16px;
    }
  }
  &__table-head-title {
    font-size: 22px;
    font-weight: 500;
    line-height: 30px;
  }
  &__table-head-form {
    position: relative;
    flex: 0 0 320px;
    width: 320px;
    margin-left: auto;
    @include response(1200px) {
      order: 3;
      width: 100%;
      flex: 0 0 100%;
    }
  }
  &__table-head-form-input {
    position: relative;
    width: 100%;
    border-radius: 99px;
    border: 1px solid var(--main-30, #bebebe);
    background: var(--Primary-White, #fff);
    height: 44px;
    padding: 4px 52px 4px 24px;
    font-family: "Instrument Sans";
    font-size: 14px;
    font-weight: 400;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: #000;
    }
    &:focus {
      border-color: #000;
    }
    &::placeholder {
      color: #8f8f8f;
      font-weight: 400;
    }
  }
  &__table-sort {
    min-width: 293px;
    @include response(1200px) {
      min-width: 160px;
      .select2-container .select2-selection--single {
        height: 36px;
      }
      .select2-container--default .select2-selection--single .select2-selection__rendered {
        line-height: 34px;
      }
    }
  }
  &__table-head-form-btn {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 14px;
    width: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0;
    background: transparent;
    padding: 0;
    @include desktop-hover() {
      svg {
        fill: $red;
      }
    }
    svg {
      fill: #4d6562;
      transition: 0.3s ease-in-out;
    }
  }
  &__table-list {
    position: relative;
    margin-bottom: 26px;
    @include response(1200px) {
      border-top: 1px solid var(--Line-Light, #eaeaea);
      border-bottom: 1px solid var(--Line-Light, #eaeaea);
      padding: 16px;
      margin-left: -16px;
      margin-right: -16px;
      background: var(--Bg-Light-gray, #f5f5f5);
      margin-bottom: 16px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__table-list-head {
    position: relative;
    background: #f5f5f5;
    display: flex;
    @include response(1200px) {
      display: none;
    }
  }
  &__table-cell {
    position: relative;
    padding: 8px 12px;
    display: flex;
    align-items: center;
    overflow: hidden;
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    transition: color 0.3s ease-in-out;
    &.js-open-types-popup {
      cursor: pointer;
    }
    &:first-child {
      padding-left: 16px;
    }
    &:last-child {
      padding-right: 16px;
      @include response(1200px) {
        padding-right: 0;
      }
    }
    @include response(1200px) {
      padding: 0;
    }
    &--head {
      color: var(--Main-Gray, #595959);
      font-size: 16px;
      line-height: 24px;
      padding-top: 4px;
      padding-bottom: 4px;
      font-weight: 400;
    }
    &:nth-child(1) {
      flex: 0 0 88px;
      width: 88px;
      @include response(1200px) {
        display: none;
      }
    }
    &:nth-child(2) {
      flex: 0 0 280px;
      width: 280px;
      @include response(1200px) {
        flex: 0 0 100%;
        width: 100%;
      }
    }
    &:nth-child(3) {
      flex: 0 0 344px;
      width: 344px;
      @include response(1200px) {
        flex: 0 0 100%;
        width: 100%;
        img {
          width: 100%;
        }
      }
    }
    &:nth-child(4) {
      flex: 0 0 280px;
      width: 280px;
      @include response(1200px) {
        width: auto;
        flex: 1 1 auto;
      }
    }
    &:nth-child(5) {
      flex: 1 1 auto;
      @include response(1200px) {
        flex: 0 0 auto;
      }
    }
  }
  &__table-cell-arrow {
    display: none;
    @include response(1200px) {
      display: block;
      margin-left: auto;
    }
  }
  &__table-body {
    position: relative;
  }
  &__table-item {
    display: flex;
    border-bottom: 1px solid var(--Line-Light, #eaeaea);
    position: relative;
    @include response(1200px) {
      align-items: flex-start;
      margin-bottom: 8px;
      border-radius: 8px;
      border: 1px solid var(--Line-Light, #eaeaea);
      background: var(--Primary-White, #fff);
      flex-wrap: wrap;
      column-gap: 12px;
      row-gap: 8px;
      padding: 12px 16px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    @include desktop-hover() {
      &::before {
        transform: scaleX(1);
      }
      .types__table-cell:nth-child(2) {
        color: #ff3d00;
      }
    }
    &::before {
      content: "";
      position: absolute;
      border-left: 2px solid var(--Primary-Accent, #ff3d00);
      background: linear-gradient(270deg, rgba(255, 237, 231, 0) 0%, #ffede7 100%);
      width: 356px;
      left: 0;
      top: 0;
      bottom: 0;
      pointer-events: none;
      transform: scaleX(0);
      transform-origin: 0 50%;
      transition: 0.3s ease-in-out;
    }
  }
  &__table-country-list {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
  }
  &__table-country {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    border-radius: 78px;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding: 4px 16px 4px 8px;
    color: var(--Primary-Black, #000);
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
  }
  &__popular {
    position: relative;
    &--line {
      display: flex;
      align-items: center;
      column-gap: 10px;
    }
  }
  &__popular-title {
    position: relative;
    margin-bottom: 4px;
    color: var(--Primary-Black, #000);
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    .types__popular--line & {
      font-size: 14px;
    }
  }
  &__popular-line {
    width: 192px;
    position: relative;
    display: flex;
    column-gap: 4px;
    border-radius: 16px;
    padding: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    > span {
      position: relative;
      width: 100%;
      border-radius: 4px;
      background: #d8d8d8;
      height: 4px;
      overflow: hidden;
      &:nth-child(1) {
        > span {
          opacity: 0.6;
        }
      }
      &:nth-child(2) {
        > span {
          opacity: 0.7;
        }
      }
      &:nth-child(3) {
        > span {
          opacity: 0.8;
        }
      }
      &:nth-child(4) {
        > span {
          opacity: 0.9;
        }
      }
      &.active {
        > span {
          display: block;
        }
      }
      > span {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: #0d9a88;
        display: none;
      }
    }
  }
  &__table-total {
    position: relative;
    margin-bottom: 16px;
    color: var(--Main-Gray, #595959);
    text-align: center;
    line-height: 20px;
  }
  &__table-more {
    text-align: center;
  }
  &__table-more-btn {
    min-width: 212px;
  }
}
