.popup-parts {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transform: translateX(100%);
  &.active {
    opacity: 1;
    visibility: visible;
    transform: none;
  }
  &__overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.4);
    opacity: 0;
    transition: 0.3s ease-in-out;
    .popup-parts.active & {
      opacity: 1;
    }
  }
  &__box {
    position: relative;
    width: 680px;
    flex: 0 0 680px;
    height: 100%;
    background: #fff;
    display: flex;
    flex-direction: column;
    opacity: 0;
    transition: 0.3s ease-in-out;
    .popup-parts.active & {
      opacity: 1;
    }
    @include response(1200px) {
      flex: 0 0 100%;
      width: 100%;
    }
  }
  &__head {
    position: relative;
    padding: 16px;
    flex: 0 0 auto;
    @include response(1200px) {
      padding: 12px 16px;
    }
  }
  &__close {
    position: absolute;
    top: 16px;
    right: 16px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s ease-in-out;
    z-index: 1;
    @include response(1200px) {
      top: 12px;
    }
    @include desktop-hover() {
      svg {
        stroke: $red;
      }
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
  }
  &__title {
    position: relative;
    margin-bottom: 16px;
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    @include response(1200px) {
      font-size: 18px;
      line-height: 26px;
      margin-bottom: 0;
      text-align: left;
    }
  }
  &__descriptions {
    position: relative;
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    @include response(1200px) {
      display: none;
    }
    p {
      margin: 0;
    }
  }
  &__body {
    position: relative;
    height: 100%;
    overflow-y: auto;
    container-type: size;
  }
  &__options {
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__tabs-list {
    display: flex;
    justify-content: center;
    padding-bottom: 16px;
    border-bottom: 1px solid var(--Line-Light, #eaeaea);
    @include response(1200px) {
      padding-bottom: 0;
      border-bottom: 0;
    }
    ul {
      display: flex;
      column-gap: 8px;
      list-style: none;
      padding: 4px;
      margin: 0;
      border-radius: 8px;
      background: var(--Bg-Light-gray, #f5f5f5);
      @include response(1200px) {
        border-radius: 0;
        width: 100%;
        padding-left: 12px;
        overflow-x: auto;
      }
      li {
        position: relative;
      }
      a {
        position: relative;
        display: block;
        text-decoration: none;
        color: #8f8f8f;
        font-size: 16px;
        font-weight: 500;
        line-height: 24px;
        padding: 4px 12px;
        transition: 0.3s ease-in-out;
        @include desktop-hover() {
        }
        &.active {
          color: #000;
          border-radius: 8px;
          background: var(--Primary-White, #fff);
          box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.04);
        }
      }
    }
  }
  &__options-tab {
    position: relative;
    @include response(1200px) {
      display: flex;
      justify-content: center;
      &.js-get-part-parent {
        flex-direction: column;
        align-items: center;
      }
    }
  }
  &__overview {
    text-align: center;
    position: relative;
    picture {
      display: block;
      @include response(1200px) {
        img {
          max-width: 375px;
          width: 100%;
        }
      }
    }
  }
  &__overview-label {
    position: absolute;
    font-weight: 600;
    font-size: 16px;
    line-height: 155%;
    color: #01231f;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: #ff3d00;
      .popup-parts__overview-label-line {
        background: #ff3d00;
        opacity: 0.5;
      }
    }
    span {
      display: block;
      position: relative;
      z-index: 1;
    }
    img {
      position: absolute;
      top: -1px;
      left: -24px;
      width: 140px;
      height: 33px;
      max-width: none;
    }
  }
  &__overview-label-line {
    position: absolute;
    background: #80918f;
    height: 77px;
    width: 1px;
    left: 50%;
    top: 100%;
    pointer-events: none;
    transition: 0.3s ease-in-out;
    &--top {
      top: auto;
      bottom: 100%;
    }
  }
  &__label {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 2;
    transition: 0.2s ease-in-out;
    .js-get-part-parent.active & {
      opacity: 0;
      visibility: hidden;
      &.active {
        opacity: 1;
        visibility: visible;
        .popup-parts__label-line {
          background: #ff3d00;
          opacity: 0.5;
        }
        .popup-parts__label-title,
        .popup-parts__label-text {
          color: #ff3d00;
        }
        .popup-parts__label-icon {
          svg {
            fill: #ff3d00;
          }
        }
      }
    }
    &--ltr {
      flex-direction: row;
    }
    &--btt {
      flex-direction: column-reverse;
    }
    &--rtl {
      flex-direction: row-reverse;
    }
    @include response(1200px) {
      &--mobile-rtl {
        flex-direction: row-reverse;
      }
      &--mobile-ltr {
        flex-direction: row;
      }
      &--mobile-btt {
        flex-direction: column-reverse;
      }
      &--mobile-ttb {
        flex-direction: column;
      }
    }
  }
  &__label-line {
    position: relative;
    background: #80918f;
    width: 1px;
    height: 80px;
    transition: 0.3s ease-in-out;
    .popup-parts__label--rtl &,
    .popup-parts__label--ltr & {
      height: 1px;
      width: 80px;
      @include response(1200px) {
        width: 70px;
      }
    }
    @include response(1200px) {
      height: 70px;
      .popup-parts__label--mobile-ltr &,
      .popup-parts__label--mobile-rtl & {
        height: 1px;
        width: 70px;
      }
      .popup-parts__label--mobile-btt & {
        height: 70px;
        width: 1px;
      }
      .popup-parts__label--mobile-long-line.popup-parts__label--mobile-btt & {
        height: 147px;
      }
    }
  }
  &__label-box {
    position: relative;
    text-align: center;
    .popup-parts__label--ltr & {
      text-align: right;
    }
    .popup-parts__label--rtl & {
      text-align: left;
    }
    @include response(1200px) {
      .popup-parts__label--mobile-rtl & {
        text-align: left;
      }
      .popup-parts__label--mobile-ltr & {
        text-align: right;
      }
    }
  }
  &__label-title {
    position: relative;
    font-weight: 600;
    font-size: 16px;
    color: #01231f;
    transition: 0.3s ease-in-out;
  }
  &__label-text {
    color: #80918f;
    font-weight: 400;
    font-size: 12px;
    line-height: 135%;
    transition: 0.3s ease-in-out;
  }
  &__label-icon {
    display: flex;
    padding: 2px;
    svg {
      transition: 0.3s ease-in-out;
      fill: #80918f;
    }
  }
  &__child-picture {
    position: relative;
    text-align: center;
  }
  &__child-img {
    @include response(1200px) {
      max-width: 375px;
      width: 100%;
    }
  }
  &__child-picture-decor {
    position: absolute;
    top: -19px;
    left: -35px;
  }
  &__child-info {
    position: absolute;
    top: 50px;
    left: 35px;
    max-width: 277px;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s ease-in-out;
    .js-get-part-parent.active & {
      opacity: 1;
      visibility: visible;
    }
    @include response(1200px) {
      position: relative;
      top: auto;
      left: auto;
      max-width: 100%;
      text-align: center;
      padding: 0 16px 40px;
      margin-top: -60px;
      img {
        display: none;
      }
    }
  }
  &__child-title {
    position: relative;
    margin-bottom: 12px;
    font-weight: 400;
    font-size: 16px;
    line-height: 155%;
    color: #01231f;
    @include response(1200px) {
      font-size: 14px;
      line-height: 135%;
    }
  }
  &__child-customisable-with {
    position: relative;
    padding-top: 8px;
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 12px;
    line-height: 135%;
    text-transform: uppercase;
    color: #b3bdbc;
    @include response(1200px) {
      padding-top: 0;
      margin-bottom: 4px;
    }
    &::before {
      content: "";
      position: absolute;
      background: #b3bdbc;
      width: 33px;
      height: 1px;
      top: 0;
      left: 0;
      @include response(1200px) {
        display: none;
      }
    }
  }
  &__child-customisable-value {
    position: relative;
    font-weight: 400;
    font-size: 12px;
    line-height: 135%;
    color: #01231f;
    @include response(1200px) {
      font-size: 14px;
    }
  }
  &__child-btn {
    display: none;
    @include response(1200px) {
      position: absolute;
      top: 16px;
      left: 16px;
      display: flex;
      align-items: center;
      font-weight: 700;
      font-size: 14px;
      line-height: 135%;
      color: #80918f;
      visibility: hidden;
      opacity: 0;
      z-index: 1;
      transition: 0.3s ease-in-out;
      .js-get-part-parent.active & {
        visibility: visible;
        opacity: 1;
      }
      svg {
        flex: 0 0 auto;
        fill: #80918f;
        margin-right: 6px;
      }
    }
  }
}

@container (height < 612px) {
  .popup-parts__options {
    justify-content: flex-start;
  }
}
