.popup {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  background: var(--Mask-Black---30, rgba(0, 0, 0, 0.3));
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: auto;
  overscroll-behavior: none;
  visibility: hidden;
  padding: 16px;
  opacity: 0;
  transform: translateY(-100vh);
  transition: opacity 0.3s ease-in-out;
  @include response(1200px) {
    @media (orientation: landscape) {
      align-items: flex-start;
    }
  }
  &.active {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
  }
  &__overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__box {
    position: relative;
    border-radius: 8px;
    background: var(--Primary-White, #fff);
    width: 100%;
    padding: 40px 16px 16px;
    transform: translateY(20px);
    opacity: 0;
    max-height: 100dvh;
    overflow-y: auto;
    overscroll-behavior: none;
    transition: 0.3s ease-in-out;
    .popup.active & {
      transform: translateY(0);
      opacity: 1;
    }
    &--fixed-head {
      overflow: initial;
      max-height: 90dvh;
      display: flex;
      flex-direction: column;
      padding: 0;
    }
  }
  &__head {
    position: relative;
    flex: 0 0 auto;
    position: relative;
    border-bottom: 1px solid transparent;
    padding: 12px 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 1;
    transition: 0.3s ease-in-out;
    &.scroll {
      box-shadow: 0px 4px 8px 0px rgba(1, 35, 31, 0.05);
      border-bottom-color: #eaeaea;
    }
  }
  &__head-number {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 4px;
    padding: 2px 8px;
    border-radius: 12px;
    background: var(--Bg-Light-green, #ebf8f7);
    color: var(--Secondary-Dark-green, #08584e);
    font-weight: 500;
    line-height: 20px;
    span {
      color: #000;
    }
  }
  &__head-title {
    position: relative;
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    opacity: 0;
    transition: 0.3s ease-in-out;
    .popup__head.scroll & {
      opacity: 1;
    }
  }
  &__type-img {
    position: relative;
    margin-bottom: 16px;
    img {
      width: 100%;
    }
  }
  &__type-title {
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    margin-bottom: 16px;
  }
  &__type-popular {
    display: flex;
    justify-content: center;
    margin-bottom: 16px;
  }
  &__type-box {
    position: relative;
    margin-bottom: 16px;
    border-radius: 8px;
    padding: 16px;
    border: 1px solid var(--Line-Light, #eaeaea);
  }
  &__type-row {
    position: relative;
    display: flex;
    column-gap: 12px;
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0;
    }
    &--mobile-full-row {
      @include response(1200px) {
        display: block;
      }
    }
  }
  &__type-cell {
    position: relative;
    font-size: 16px;
    line-height: 24px;
    &:nth-child(1) {
      color: var(--Main-Gray, #595959);
      flex: 0 0 140px;
      width: 140px;
    }
    &:nth-child(2) {
      color: var(--Primary-Black, #000);
      font-weight: 500;
    }
  }
  &__body {
    position: relative;
    flex: 1 1 auto;
    overflow-y: auto;
    overscroll-behavior: none;
    padding: 16px;
  }
  &__footer {
    position: relative;
    flex: 0 0 auto;
    display: flex;
    border-top: 1px solid var(--Line-Light, #eaeaea);
    background: var(--Bg-Light-gray, #f5f5f5);
    border-radius: 0 0 8px 8px;
  }
  &__footer-btn {
    flex: 0 0 50%;
    width: 50%;
    display: flex;
    align-items: center;
    column-gap: 8px;
    color: var(--Primary-Black, #000);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    padding: 4px 16px;
    cursor: pointer;
    border-right: 1px solid #eaeaea;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
    &:last-child {
      border-right: 0;
    }
    &:nth-child(2) {
      justify-content: flex-end;
      text-align: right;
    }
    span {
      max-height: 48px;
      overflow: hidden;
      -webkit-line-clamp: 2;
      display: box;
      display: -webkit-box;
      -webkit-box-orient: vertical;
    }
    svg {
      stroke: $red;
      flex: 0 0 auto;
    }
  }
  &__close {
    position: absolute;
    top: 16px;
    right: 16px;
    cursor: pointer;
    z-index: 1;
    display: flex;
    @include desktop-hover() {
      svg {
        stroke: #ff3d00;
      }
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
    &--relative {
      position: relative;
      top: auto;
      right: auto;
    }
  }
  &__tags-nav-holder {
    position: relative;
    display: flex;
    justify-content: center;
    margin-bottom: 32px;
  }
  &__tags-nav {
    position: relative;
    list-style: none;
    padding: 4px;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    background: #f5f5f5;
    gap: 8px;
    @include response(1200px) {
      gap: 0;
    }
    &.mobile-start-items {
      @include response(1200px) {
        justify-content: flex-start;
        flex: 0 0 auto;
      }
    }
    li {
      position: relative;
      flex: 0 0 auto;
    }
    a {
      display: block;
      text-decoration: none;
      padding: 4px 12px;
      color: #969696;
      text-align: center;
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      color: #969696;
      border-radius: 8px;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        background: #fff;
        box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.04);
        color: #000;
      }
      &.active {
        background: #fff;
        box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.04);
        color: #000;
      }
    }
  }
  &__tags-options {
    position: relative;
    padding: 0 24px 16px;
  }
  &__tags-option {
    position: relative;
  }
  &__tags-title {
    position: relative;
    margin-bottom: 16px;
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    line-height: 36px;
  }
  &__tags-description {
    position: relative;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 32px;
    p {
      margin: 0;
    }
  }
  &__tags-list {
    position: relative;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  &__tags-item {
    position: relative;
    margin-bottom: 32px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__tags-item-title {
    position: relative;
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 12px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__tags-item-description {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    p {
      margin: 0;
    }
  }
  &__title {
    position: relative;
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    margin-bottom: 12px;
  }
  &__descriptions {
    position: relative;
    margin-bottom: 32px;
    color: var(--Main-Gray, #595959);
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    @include response(1200px) {
      margin-bottom: 24px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__form {
    padding: 16px 24px;
    @include response(1200px) {
      padding: 0 16px;
    }
  }
  &__form-forgot {
    margin-bottom: 32px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include response(1200px) {
      margin-bottom: 24px;
    }
    .checkbox-style {
      margin-bottom: 0;
    }
  }
  &__form-forgot-btn {
    position: relative;
    cursor: pointer;
    color: var(--Primary-Accent, #ff3d00);
    line-height: 20px;
    text-decoration: underline;
    text-underline-offset: 3px;
    @include response(375px) {
      font-size: 12px;
    }
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  &__form-btn {
    width: 100%;
    margin-bottom: 32px;
    @include response(1200px) {
      margin-bottom: 24px;
    }
  }
  &__form-continue {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    text-align: center;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
  }
  &__builder {
    position: relative;
  }
  &__builder-head {
    position: relative;
    display: flex;
    column-gap: 12px;
    padding-bottom: 12px;
    margin-top: -24px;
    padding-right: 30px;
  }
  &__builder-details {
    position: relative;
    margin-left: -16px;
    margin-right: -16px;
  }
  &__builder-text {
    max-height: 30vh;
    overflow-y: auto;
    padding-right: 4px;
    &::-webkit-scrollbar {
      display: block;
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #fff;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #01231f;
      border-radius: 4px;
    }
  }
}

.return-policy {
  position: relative;
  padding: 16px 24px;
  &__head {
    position: relative;
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    gap: 16px;
    @include response(767px) {
      display: block;
    }
  }
  &__img {
    position: relative;
    flex: 0 0 32px;
    display: flex;
  }
  &__head-title {
    font-size: 24px;
    font-weight: 500;
    line-height: 36px;
    span {
      color: #ff3d00;
    }
  }
  &__body {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 32px;
    p {
      margin: 0;
    }
  }
  &__btn {
    position: relative;
    text-decoration: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    cursor: pointer;
    border-radius: 8px;
    border: 1px solid #d3d3d3;
    background: var(--Primary-White, #fff);
    padding: 7px 12px;
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: #000;
    }
  }
}

.notify-me {
  position: relative;
  padding: 16px 24px;
  &--mobile-padding {
    @include response(1200px) {
      padding: 0;
    }
  }
  &__head {
    position: relative;
    @include response(1200px) {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8px;
      margin-bottom: 15px;
    }
  }
  &__icon {
    display: block;
    margin: 0 auto 16px;
    @include response(1200px) {
      .notify-me__head & {
        margin: 0;
      }
    }
  }
  &__description {
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 20px;
  }
  &__title {
    position: relative;
    margin-bottom: 24px;
    color: #000;
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    line-height: 36px;
    @include response(1200px) {
      .notify-me__head & {
        font-size: 20px;
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__body {
    margin-bottom: 24px;
  }
  &__btn-box {
    text-align: center;
  }
  &__btn {
    position: relative;
  }
  &__footer {
    color: #4d6562;
    text-align: center;
    font-size: 12px;
    line-height: 20px;
    margin-top: 16px;
    a {
      text-decoration: none;
      color: #ff3d00;
      @include desktop-hover() {
        text-decoration: underline;
      }
    }
  }
}

.configuration {
  position: relative;
  @include response(1200px) {
    max-height: calc(100dvh - 104px);
    display: flex;
    flex-direction: column;
  }
  &__title {
    position: relative;
    margin-bottom: 16px;
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    @include response(1200px) {
      flex: 0 0 auto;
    }
  }
  &__nav {
    position: relative;
    display: flex;
    justify-content: center;
    margin-bottom: 16px;
    @include response(1200px) {
      flex: 0 0 auto;
      overflow-x: auto;
      margin-left: -16px;
      margin-right: -16px;
      padding-left: 16px;
      padding-right: 16px;
      justify-content: flex-start;
    }
  }
  &__options {
    position: relative;
    padding: 16px 24px;
    @include response(1200px) {
      padding: 0;
      flex: 1 1 auto;
      overflow-y: auto;
      margin-right: -8px;
      padding-right: 8px;
      overscroll-behavior: none;
      &::-webkit-scrollbar {
        display: block;
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #eaeaea;
      }
      &::-webkit-scrollbar-thumb {
        background-color: #01231f;
        border-radius: 4px;
      }
    }
  }
  &__tab {
    position: relative;
  }
  &__list {
    position: relative;
  }
  &__row {
    position: relative;
    padding-top: 16px;
    padding-bottom: 16px;
    border-top: 1px solid #eaeaea;
    display: flex;
    align-items: center;
    gap: 8px;
    @include response(1200px) {
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }
  &__cell {
    position: relative;
    &:first-child {
      flex: 1 1 auto;
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      display: flex;
      flex-wrap: wrap;
      column-gap: 4px;
      span {
        font-weight: 500;
        @include response(767px) {
          width: 100%;
        }
      }
    }
    &:last-child {
      text-align: right;
      font-size: 16px;
      font-weight: 700;
      line-height: 24px;
      flex: 0 0 auto;
    }
  }
}
