.video-popup {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.72);
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease-in-out;
  &.active {
    opacity: 1;
    visibility: visible;
  }
  &__slider {
    height: 100%;
  }
  &__slide {
    position: relative;
    display: flex;
    height: 100%;
    overflow: hidden;
  }
  &__box {
    position: relative;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    @include response(1200px) {
      transition: 0.3s ease-in-out;
      .active-info & {
        opacity: 0;
        visibility: hidden;
      }
    }
    @include desktop-hover() {
      &.is-playing {
        .video-item__pause {
          opacity: 1;
        }
      }
    }
  }
  &__aside-mobile-btn {
    display: none;
    @include response(1200px) {
      position: absolute;
      right: 12px;
      bottom: calc(100% + 16px);
      width: 40px;
      height: 40px;
      border-radius: 50%;
      border: 1px solid rgba(255, 255, 255, 0.2);
      background: rgba(255, 255, 255, 0.12);
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0;
      visibility: hidden;
      .active-info & {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &__aside {
    position: relative;
    flex: 0 0 480px;
    max-width: 480px;
    width: 100%;
    background: #fff;
    display: flex;
    flex-direction: column;
    @include response(1200px) {
      position: absolute;
      left: 0;
      right: 0;
      top: 80px;
      bottom: 0;
      transform: translateY(100%);
      max-width: 100%;
      border-radius: 16px 16px 0 0;
      transition: 0.2s ease-in-out;
      .active-info & {
        transform: translateY(0);
      }
    }
  }
  &__aside-inner {
    position: relative;
    flex: 1 1 auto;
    overflow-y: auto;
    overscroll-behavior: contain;
  }
  &__source {
    height: 100%;
    max-width: 100%;
  }
  &__close {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.2);
    background: rgba(255, 255, 255, 0.12);
    position: absolute;
    width: 40px;
    height: 40px;
    top: 18px;
    left: 30px;
    z-index: 10;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    @include response(1200px) {
      top: 24px;
      left: 16px;
    }
    @include desktop-hover() {
      background: $red;
      border-color: $red;
    }
    svg {
      stroke: #fff;
      transition: 0.3s ease-in-out;
    }
  }
  &__controls {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 46px 0 16px 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000 100%);
    @include response(1200px) {
      bottom: auto;
      top: 0;
      padding: 0;
    }
  }
  &__controls-time-box {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 16px;
    color: var(--Primary-White, #fff);
    font-weight: 700;
    line-height: 20px;
    @include response(1200px) {
      display: none;
    }
  }
  &__controls-time {
    position: relative;
  }
  &__controls-progress {
    position: relative;
    height: 4px;
    background: rgba(255, 255, 255, 0.12);
    overflow: hidden;
    cursor: pointer;
    &::before {
      content: "";
      display: block;
      height: 100%;
      background: #d9d9d9;
      width: var(--progress, 0%);
      transition: width 0.1s ease;
    }
  }
  &__controls-length {
    position: relative;
  }
  &__controls-mute-box {
    position: relative;
    display: flex;
    justify-content: flex-end;
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 10px;
    @include response(1200px) {
      margin-bottom: 0;
    }
  }
  &__controls-mute {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    cursor: pointer;
    @include response(1200px) {
      position: fixed;
      right: 16px;
      top: 46%;
      margin-top: 48px;
      width: 40px;
      height: 40px;
      border: 1px solid rgba(255, 255, 255, 0.2);
      background: rgba(255, 255, 255, 0.12);
      border-radius: 50%;
    }
    .unmute {
      transition: 0.1s ease-in-out;
    }
    .mute {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      transition: 0.1s ease-in-out;
      @include response(1200px) {
        top: 50%;
        left: 50%;
        margin-top: -12px;
        margin-left: -12px;
      }
    }
    &.is-muted {
      .unmute {
        opacity: 0;
      }
      .mute {
        opacity: 1;
      }
    }
  }
  &__arrow {
    position: absolute;
    cursor: pointer;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.2);
    background: rgba(255, 255, 255, 0.12);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    width: 40px;
    height: 40px;
    right: 504px;
    top: 50%;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      right: 16px;
      top: 46%;
      transition: 0.3s ease-in-out;
      &.hide {
        opacity: 0;
        visibility: hidden;
      }
    }
    @include desktop-hover() {
      background: #fff;
      border-color: #fff;
      svg {
        stroke: #000;
      }
    }
    svg {
      stroke: #fff;
      transition: 0.3s ease-in-out;
    }
    &.swiper-button-disabled {
      opacity: 0;
      pointer-events: none;
    }
    &--prev {
      margin-top: -48px;
    }
    &--next {
      margin-bottom: -48px;
    }
    &--without-aside {
      right: 32px;
      @include response(1200px) {
        right: 16px;
      }
    }
  }
  &__aside-slider-box {
    position: relative;
    @include response(1200px) {
      border-radius: 16px 16px 0 0;
      overflow: hidden;
    }
  }
  &__aside-slider-pagination {
    padding-top: 8px;
    display: flex;
    justify-content: center;
    &--v2 {
      position: absolute;
      bottom: 12px;
      left: 50% !important;
      transform: translateX(-50%);
      padding: 4px 8px;
      border-radius: 40px;
      background: var(--Primary-White, #fff);
      width: auto !important;
      z-index: 10;
    }
  }
  &__aside-slider {
    position: relative;
  }
  &__aside-slider-item {
    position: relative;
    text-align: center;
  }
  &__aside-slider-arrow {
    position: absolute;
    top: 50%;
    margin-top: -12px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    cursor: pointer;
    z-index: 1;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      svg {
        stroke: $red;
      }
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
    &.swiper-button-disabled {
      opacity: 0;
      pointer-events: none;
    }
    &--prev {
      left: 16px;
    }
    &--next {
      right: 16px;
    }
  }
  &__aside-holder {
    position: relative;
    padding: 16px 24px;
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__aside-head {
    position: relative;
    margin-bottom: 4px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__aside-label {
    position: relative;
    text-decoration: none;
    color: $red;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $redH;
    }
  }
  &__aside-rate {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    flex: 0 0 auto;
    padding: 4px 8px;
    border-radius: 40px;
    background: var(--Bg-Light-orange, #ffede7);
  }
  &__aside-rate-val {
    position: relative;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__aside-rate-star {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    color: var(--Primary-Accent, #ff3d00);
    font-weight: 500;
    line-height: 24px;
  }
  &__aside-title {
    display: block;
    text-decoration: none;
    position: relative;
    margin-bottom: 12px;
    font-size: 20px;
    font-weight: 500;
    line-height: 30px;
    color: #000;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
  }
  &__aside-price {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;
  }
  &__aside-price-left {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 4px;
  }
  &__aside-price-val {
    font-size: 20px;
    font-weight: 500;
    line-height: 30px;
    color: #000;
  }
  &__aside-price-new {
    color: var(--Secondary-Red, #f70808);
    font-size: 20px;
    font-weight: 500;
    line-height: 30px;
  }
  &__aside-price-old {
    color: var(--main-50, #8f8f8f);
    font-weight: 500;
    line-height: normal;
    text-decoration-line: line-through;
  }
  &__aside-thumbs {
    position: relative;
    padding-top: 16px;
    border-top: 1px solid var(--Line-Light, #eaeaea);
  }
  &__aside-thumbs-head {
    position: relative;
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 10px;
  }
  &__aside-thumbs-title {
    position: relative;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__aside-thumbs-arrows {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 4px;
  }
  &__aside-thumbs-arrow {
    position: relative;
    display: flex;
    width: 32px;
    height: 32px;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    cursor: pointer;
    transition: 0.3s ease-in-out;
    &.swiper-button-disabled {
      opacity: 0;
      pointer-events: none;
    }
    @include desktop-hover() {
      border-color: $red;
    }
  }
  &__aside-thumbs-slider-holder {
    overflow: hidden;
    margin-right: -24px;
    @include response(1200px) {
      margin-right: 0;
    }
  }
  &__aside-thumbs-slider {
    overflow: visible;
  }
  &__aside-btns {
    position: relative;
    flex: 0 0 auto;
    background: #fff;
    padding: 12px 24px;
    border-top: 1px solid var(--Line-Light, #eaeaea);
    &--flex {
      display: flex;
      column-gap: 10px;
    }
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__aside-full-btn {
    width: 100%;
  }
  &__mobile-card {
    display: none;
    @include response(1200px) {
      display: block;
      position: absolute;
      bottom: 24px;
      left: 16px;
      right: 16px;
    }
  }
  &__mobile-card-head {
    @include response(1200px) {
      display: flex;
      justify-content: flex-end;
      margin-bottom: 20px;
    }
  }
  &__mobile-card-info-btn {
    @include response(1200px) {
      position: relative;
      border-radius: 50%;
      border: 1px solid rgba(255, 255, 255, 0.2);
      background: rgba(255, 255, 255, 0.12);
      backdrop-filter: blur(2px);
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  &__mobile-card-box {
    position: relative;
    border-radius: 12px;
    border: 1px solid rgba(234, 234, 234, 0.16);
    background: rgba(255, 255, 255, 0.12);
    backdrop-filter: blur(12px);
    display: flex;
    overflow: hidden;
  }
  &__mobile-card-img {
    position: relative;
    flex: 0 0 92px;
    width: 92px;
    &:not(.video-popup__mobile-card-img--v2) {
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &--v2 {
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 0 0 100px;
      width: 100px;
      padding: 8px;
      img {
        max-height: 100%;
      }
    }
  }
  &__mobile-card-holder {
    position: relative;
    padding: 12px;
    overflow: auto;
  }
  &__mobile-card-title {
    display: block;
    margin-bottom: 12px;
    white-space: nowrap;
    overflow: hidden;
    color: var(--Primary-White, #fff);
    text-overflow: ellipsis;
    line-height: 20px;
    text-decoration: none;
    &--v2 {
      margin-bottom: 4px;
      font-weight: 500;
    }
  }
  &__mobile-card-text {
    overflow: hidden;
    white-space: nowrap;
    color: var(--Primary-White, #fff);
    text-overflow: ellipsis;
    line-height: 20px;
    margin-bottom: 8px;
  }
  &__mobile-card-footer {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 10px;
  }
  &__mobile-card-price {
    color: var(--Primary-White, #fff);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__mobile-card-btn {
    position: relative;
    text-decoration: none;
    color: var(--Primary-White, #fff);
    font-weight: 500;
    line-height: 20px;
    border-radius: 56px;
    border: 1px solid rgba(255, 61, 0, 0.4);
    background: rgba(255, 61, 0, 0.2);
    backdrop-filter: blur(2px);
    padding: 8px 12px;
  }
  &__triggers {
    position: relative;
    padding: 12px;
    border-radius: 8px;
    border: 1px solid var(--Line-Light, #eaeaea);
    background: var(--Primary-White, #fff);
    display: flex;
    column-gap: 24px;
    margin-bottom: 16px;
  }
  &__triggers-item {
    position: relative;
    text-align: center;
    width: 100%;
    &::before {
      content: "";
      position: absolute;
      background: #eaeaea;
      top: 0;
      bottom: 0;
      width: 1px;
      right: -12px;
    }
    &:last-child {
      &::before {
        display: none;
      }
    }
  }
  &__triggers-item-head {
    position: relative;
    margin-bottom: 2px;
    color: var(--Main-Gray, #595959);
    line-height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 8px;
  }
  &__triggers-item-val {
    position: relative;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__short-info {
    position: relative;
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    column-gap: 12px;
  }
  &__short-info-img {
    position: relative;
    flex: 0 0 64px;
    width: 64px;
    height: 64px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__short-info-box {
    position: relative;
    overflow: auto;
  }
  &__short-info-title {
    display: block;
    text-decoration: none;
    color: var(--Primary-Black, #000);
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    margin-bottom: 2px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
  }
  &__short-info-text {
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
  }
}

.img-mobile-poster {
  display: none;
  @include response(1200px) {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 8px;
  }
}
