.info-rows {
  position: relative;
  padding-bottom: 64px;
  @include response(1200px) {
    padding-bottom: 32px;
  }
  &__item {
    position: relative;
    background: var(--Bg-Light-gray, #f5f5f5);
    margin-bottom: 40px;
    display: flex;
    border-radius: 8px;
    overflow: hidden;
    &:nth-child(odd) {
      flex-direction: row-reverse;
    }
    &:last-child {
      margin-bottom: 0;
    }
    @include response(1200px) {
      display: block;
      margin-bottom: 16px;
    }
  }
  &__item-img {
    position: relative;
    flex: 0 0 50%;
    width: 50%;
    @include response(1200px) {
      width: 100%;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-box {
    position: relative;
    padding: 32px 48px 32px 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 0 0 50%;
    width: 50%;
    @include response(1200px) {
      width: 100%;
      padding: 12px 16px 24px 16px;
      text-align: center;
    }
  }
  &__item-title {
    position: relative;
    margin-bottom: 12px;
    font-size: 28px;
    font-weight: 500;
    line-height: 36px;
    @include response(1200px) {
      font-size: 22px;
      line-height: 30px; 
    }
  }
  &__item-text {
    position: relative;
    margin-bottom: 24px;
    color: var(--Main-Gray, #595959);
    font-size: 18px;
    line-height: 24px;
  }
  &__item-btn-box {
    position: relative;
  }
  &s__item-btn {
    position: relative;
  }
}
