.progress {
  position: relative;
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  column-gap: 8px;
  @include response(1200px) {
    display: none;
  }
  &__point {
    position: relative;
    background: #f5f5f5;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    &.active {
      background: #0d9a88;
    }
    &--last {
      svg {
        fill: #d7d9d9;
      }
    }
  }
  &__point-label {
    position: absolute;
    bottom: 100%;
    color: var(--Line-Light, #eaeaea);
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    left: 50%;
    transform: translateX(-50%);
    .progress__point.active & {
      color: #0d9a88;
    }
  }
  &__point-value {
    color: var(--main-30, #bebebe);
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 16px;
    .progress__point.active & {
      color: #fff;
    }
    svg {
      display: inline-block;
      vertical-align: top;
    }
  }
  &__line {
    position: relative;
    flex: 1 1 auto;
    height: 4px;
    background: #eaeaea;
    border-radius: 50px;
    &:nth-last-child(2) {
      width: 98px;
      max-width: 98px;
      flex: 0 0 98px;
    }
  }
  &__line-inner {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    background: #0d9a88;
    border-radius: 50px;
  }
}

.progress-mobile {
  display: none;
  @include response(1200px) {
    position: sticky;
    top: 0;
    background: #fff;
    padding: 0 0 0 10px;
    background: #4d6562;
    display: flex;
    align-items: center;
    column-gap: 12px;
    z-index: 1;
  }
  @include response(1200px) {
    &__line {
      position: relative;
      flex: 1 1 auto;
      border-radius: 999px;
      height: 20px;
      background: var(--Primary-White, #fff);
    }
    &__line-value {
      position: absolute;
      left: 8px;
      top: 0;
      bottom: 0;
      color: #fff;
      font-size: 12px;
      display: flex;
      align-items: center;
    }
    &__line-inner {
      position: absolute;
      left: 2px;
      top: 2px;
      bottom: 2px;
      border-radius: 999px;
      background: var(--Secondary-Approval-Green, #0d9a88);
    }
    &__box {
      position: relative;
      display: flex;
      align-items: center;
      flex: 0 0 auto;
      column-gap: 6px;
    }
    &__price {
      position: relative;
      color: var(--Primary-White, #fff);
      line-height: 135%;
      padding-left: 14px;
      .progress-mobile.active & {
        opacity: 0;
        visibility: hidden;
      }
      span {
        font-weight: 700;
      }
    }
    &__btn {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px 4px 10px 10px;
      .progress-mobile.active & {
        opacity: 0;
        visibility: hidden;
      }
    }
    &__btn-close {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(255, 255, 255, 0.16);
      opacity: 0;
      visibility: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      .progress-mobile.active & {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}
