.principles {
  position: relative;
  background: #f5f5f5;
  @include response(1200px) {
    background: transparent;
  }
  &__box {
    position: relative;
    display: flex;
    @include response(1200px) {
      background: #f5f5f5;
      flex-direction: column-reverse;
      border-radius: 8px;
      overflow: hidden;
    }
  }
  &__img {
    position: relative;
    flex: 0 0 auto;
  }
  &__holder {
    position: relative;
    padding: 32px 48px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__title {
    position: relative;
    margin-bottom: 12px;
    color: var(--Primary-Main, #01231f);
    font-size: 28px;
    font-style: normal;
    font-weight: 500;
    line-height: 36px;
    @include response(1200px) {
      font-size: 24px;
      line-height: 32px;
    }
  }
  &__text {
    position: relative;
    color: var(--Primary-Main, #01231f);
    font-size: 16px;
    line-height: 24px;
    p {
      margin: 0 0 24px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
