.header {
  position: relative;
  z-index: 2;
  &__label {
    position: relative;
    background: #4d6562;
    padding: 10px 16px;
    color: var(--Primary-White, #fff);
    text-align: center;
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    @include response(1200px) {
      padding-top: 5px;
      padding-bottom: 5px;
    }
    a {
      color: #fff;
      font-weight: 700;
      @include desktop-hover() {
        text-decoration: none;
      }
    }
    span {
      font-weight: 700;
    }
  }
  &__top {
    position: relative;
    background: var(--Primary-Main, #01231f);
    padding-top: 12px;
    padding-bottom: 12px;
  }
  &__top-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__left {
    display: flex;
    align-items: center;
    @include response(1200px) {
      gap: 24px;
    }
  }
  &__menu-btn {
    display: none;
    @include response(1200px) {
      display: flex;
      width: 20px;
      height: 18px;
      position: relative;
      span {
        position: absolute;
        border-radius: 3px;
        left: 0;
        width: 20px;
        height: 3px;
        background-color: #fff;
        transition: all 0.3s ease-in-out;
        &:nth-child(1) {
          top: 0;
        }
        &:nth-child(2) {
          top: calc(50% - 1.5px);
        }
        &:nth-child(3) {
          bottom: 0;
        }
      }
      &.active {
        span {
          &:nth-child(1) {
            transform: rotate(45deg);
            transform-origin: 20% 30%;
            top: 5px;
          }
          &:nth-child(2) {
            width: 0;
          }
          &:nth-child(3) {
            transform: rotate(-45deg);
            transform-origin: 15% 66%;
            bottom: 1px;
          }
        }
      }
    }
  }
  &__logo {
    position: relative;
    flex: 0 0 auto;
    margin-right: 32px;
    text-decoration: none;
    @include response(1200px) {
      margin-right: 0;
    }
  }
  &__right {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 24px;
  }
  &__second-nav {
    position: relative;
    flex: 0 0 auto;
    @include response(1200px) {
      display: none;
    }
    @include desktop-hover() {
      .header__second-nav-arrow {
        transform: scaleY(-1);
      }
      > ul {
        opacity: 1;
        visibility: visible;
      }
    }
    > ul {
      list-style: none;
      margin: 0;
      padding: 8px;
      position: absolute;
      top: 100%;
      left: 0;
      border-radius: 8px;
      background: var(--background-bg-white-pages, #fff);
      box-shadow: 0px 12px 40px 0px rgba(0, 0, 0, 0.1);
      width: 260px;
      visibility: hidden;
      opacity: 0;
      z-index: 2;
      transition: 0.3s ease-in-out;
      > li {
        @include desktop-hover() {
          > a {
            background: #f5f5f5;
            color: #000;
            color: var(--Primary-Accent, #ff3d00);
          }
        }
        > a {
          display: block;
          position: relative;
          padding: 12px 24px 12px 12px;
          color: var(--Primary-Black, #000);
          font-size: 16px;
          line-height: 24px;
          text-decoration: none;
          border-radius: 8px;
          transition: 0.3s ease-in-out;
        }
      }
    }
  }
  &__second-nav-title {
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;
    color: #fff;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
  }
  &__second-nav-icon {
    position: relative;
    flex: 0 0 auto;
    fill: #fff;
    margin-right: 6px;
  }
  &__second-nav-arrow {
    position: relative;
    flex: 0 0 auto;
    fill: #fff;
    margin-left: 6px;
    fill: none;
    stroke: #fff;
    transition: 0.3s ease-in-out;
  }
  &__controls {
    position: relative;
    display: flex;
    align-items: center;
    gap: 20px;
    flex: 0 0 auto;
    @include response(1200px) {
      gap: 24px;
    }
  }
  &__controls-item {
    position: relative;
    flex: 0 0 32px;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    cursor: pointer;
    @include desktop-hover() {
      .header__controls-item-icon {
        display: none;
      }
      .header__controls-item-icon-active {
        display: block;
      }
    }
    @include response(1200px) {
      flex: 0 0 24px;
      width: 24px;
      height: 24px;
    }
    &--profile {
      @include response(1200px) {
        display: none;
      }
    }
    &--active {
      .header__controls-item-icon {
        display: none;
      }
      .header__controls-item-icon-active {
        display: block;
      }
    }
  }
  &__controls-item-icon {
    position: relative;
    width: 100%;
    height: 100%;
  }
  &__controls-item-icon-active {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
  }
  &__controls-item-counter {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 99px;
    background: var(--Primary-Accent, #ff3d00);
    color: var(--Primary-White, #fff);
    text-align: center;
    font-size: 12px;
    line-height: 14px;
    font-weight: 500;
    height: 14px;
    min-width: 14px;
    padding-left: 4px;
    padding-right: 4px;
  }
  &__bottom {
    position: relative;
    border-bottom: 1px solid #eaeaea;
    background: var(--Primary-White, #fff);
    @include response(1200px) {
      display: none;
    }
  }
  &__nav {
    position: relative;
    > ul {
      position: relative;
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      gap: 32px;
      z-index: 1;
      @include response(1200px) {
        display: block;
      }
      > li {
        position: relative;
        @include response(1200px) {
          border-bottom: 1px solid #eaeaea;
        }
        &.menu-item-has-children {
          @include response(1200px) {
            &.active {
              > a {
                color: #646464;
                justify-content: center;
                border-bottom: 1px solid #eaeaea;
                > svg {
                  display: none;
                }
              }
              > ul {
                display: block;
              }
              &.level {
                > ul {
                  > li {
                    border-bottom: 0;
                  }
                }
                > a {
                  display: none;
                }
              }
            }
          }
          > a {
            padding-right: 22px;
            @include response(1200px) {
              padding-right: 30px;
            }
            .nav-icon {
              fill: #4d6562;
              transition: 0.3s ease-in-out;
            }
            > svg:not(.nav-icon) {
              position: absolute;
              top: 50%;
              margin-top: -8px;
              right: 0;
              stroke: #000;
              transition: 0.3s ease-in-out;
              @include response(1200px) {
                right: 12px;
                transform: rotate(-90deg);
              }
            }
          }
        }
        @include desktop-hover() {
          > .header__nav-level-box {
            visibility: visible;
            opacity: 1;
            transform: translateY(0) translateX(-50%);
          }
          > ul {
            visibility: visible;
            opacity: 1;
          }
          > a {
            color: var(--Primary-Accent, #ff3d00);
            .nav-icon {
              fill: $red;
            }
            &::before {
              transform: scaleX(1);
            }
            > svg:not(.nav-icon) {
              stroke: var(--Primary-Accent, #ff3d00);
              transform: scaleY(-1);
            }
          }
        }
        > a {
          position: relative;
          display: flex;
          align-items: center;
          column-gap: 6px;
          text-decoration: none;
          color: var(--Primary-Black, #000);
          font-size: 16px;
          font-weight: 500;
          line-height: 24px;
          padding: 11px 0;
          @include response-min(1201px) {
            transition: 0.3s ease-in-out;
          }
          @include response(1200px) {
            font-size: 18px;
            line-height: 26px;
            padding-top: 12px;
            padding-bottom: 12px;
          }
          &::before {
            content: "";
            position: absolute;
            height: 2px;
            bottom: 0;
            left: 0;
            right: 0;
            background: $red;
            transform: scaleX(0);
            transition: 0.3s ease-in-out;
          }
        }
        > ul {
          list-style: none;
          margin: 0;
          padding: 8px;
          position: absolute;
          top: calc(100% + 1px);
          left: 0;
          border-radius: 8px;
          background: var(--background-bg-white-pages, #fff);
          box-shadow: 0px 12px 40px 0px rgba(0, 0, 0, 0.1);
          width: 220px;
          visibility: hidden;
          opacity: 0;
          transition: 0.3s ease-in-out;
          @include response(1200px) {
            display: none;
            opacity: 1;
            visibility: visible;
            width: 100%;
            box-shadow: none;
            position: relative;
            top: auto;
            left: auto;
            padding: 0;
          }
          &::before {
            content: "";
            position: absolute;
            height: 1px;
            left: 0;
            right: 0;
            top: -1px;
          }
          > li {
            @include response(1200px) {
              border-bottom: 1px solid #eaeaea;
              &:last-child {
                border-bottom: 0;
              }
            }
            &.menu-item-has-children {
              @include response(1200px) {
                &.active {
                  > a {
                    color: #646464;
                    text-align: center;
                    border-bottom: 1px solid #eaeaea;
                    > svg {
                      display: none;
                    }
                  }
                  > ul {
                    display: block;
                  }
                }
              }
              > a {
                > svg {
                  position: absolute;
                  top: 50%;
                  margin-top: -8px;
                  right: 12px;
                  stroke: #000;
                  transition: 0.3s ease-in-out;
                }
              }
            }
            @include desktop-hover() {
              > ul {
                visibility: visible;
                opacity: 1;
              }
              > a {
                background: #f5f5f5;
                color: #000;
                color: var(--Primary-Accent, #ff3d00);
                > svg {
                  stroke: var(--Primary-Accent, #ff3d00);
                }
              }
            }
            > a {
              display: block;
              position: relative;
              padding: 12px 24px 12px 12px;
              color: var(--Primary-Black, #000);
              font-size: 16px;
              line-height: 24px;
              text-decoration: none;
              border-radius: 8px;
              @include response-min(1201px) {
                transition: 0.3s ease-in-out;
              }
              @include response(1200px) {
                padding: 12px 0;
                font-size: 18px;
                font-weight: 500;
                line-height: 26px;
              }
            }
            > ul {
              list-style: none;
              margin: 0;
              padding: 8px;
              position: absolute;
              top: 0;
              left: 100%;
              border-radius: 8px;
              background: var(--background-bg-white-pages, #fff);
              box-shadow: 0px 12px 40px 0px rgba(0, 0, 0, 0.1);
              width: 220px;
              visibility: hidden;
              opacity: 0;
              transition: 0.3s ease-in-out;
              @include response(1200px) {
                display: none;
                opacity: 1;
                visibility: visible;
                width: 100%;
                box-shadow: none;
                position: relative;
                top: auto;
                left: auto;
                padding: 0;
              }
              > li {
                @include response(1200px) {
                  border-bottom: 1px solid #eaeaea;
                  &:last-child {
                    border-bottom: 0;
                  }
                }
                @include desktop-hover() {
                  > a {
                    background: #f5f5f5;
                    color: #000;
                    color: var(--Primary-Accent, #ff3d00);
                  }
                }
                > a {
                  display: block;
                  position: relative;
                  padding: 12px 24px 12px 12px;
                  color: var(--Primary-Black, #000);
                  font-size: 16px;
                  line-height: 24px;
                  text-decoration: none;
                  border-radius: 8px;
                  @include response(1200px) {
                    padding: 12px 0;
                    font-size: 18px;
                    font-weight: 500;
                    line-height: 26px;
                  }
                  @include response-min(1201px) {
                    transition: 0.3s ease-in-out;
                  }
                }
              }
            }
          }
        }
      }
    }
    &--new {
      > ul > li:not(.menu-item-has-children--relative) {
        position: initial;
      }
    }
  }
  &__mobile {
    display: none;
    @include response(1200px) {
      overscroll-behavior: none;
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      top: 100%;
      height: calc(100dvh - 57px);
      z-index: 3;
      background: #fff;
      padding: 24px 16px;
      opacity: 0;
      visibility: hidden;
      overflow-y: auto;
      &.active {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &__nav-level-box {
    position: absolute;
    top: calc(100% + 1px);
    width: 100vw;
    left: 50%;
    transform: translateX(-50%) translateY(10px);
    background: var(--Primary-White, #fff);
    box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.08);
    padding: 24px 80px;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
    @include response(1200px) {
      opacity: 1;
      visibility: visible;
      transform: none;
      position: relative;
      left: auto;
      top: auto;
      box-shadow: none;
      padding: 0;
      width: 100%;
      display: none;
      .menu-item-has-children.active & {
        display: block;
      }
    }
    &::before {
      content: "";
      position: absolute;
      height: 1px;
      left: 0;
      right: 0;
      bottom: 100%;
    }
    > .container {
      @include response(1200px) {
        padding-left: 0;
        padding-right: 0;
      }
      ul {
        display: flex;
        margin: 0 0 24px;
        padding: 0;
        column-gap: 16px;
        list-style: none;
        &:last-child {
          margin-bottom: 0;
        }
        @include response(1200px) {
          display: block;
          margin: 0;
        }
        > li {
          position: relative;
          width: 243px;
          flex: 0 0 243px;
          @include response(1200px) {
            width: 100%;
            border-bottom: 1px solid #eaeaea;
            &:last-child {
              border-bottom: 0;
            }
          }
          > a:not(.custom-link) {
            display: block;
            position: relative;
            text-decoration: none;
            border-radius: 4px;
            background: var(--Bg-Light-gray, #f5f5f5);
            overflow: hidden;
            height: 140px;
            padding: 20px 16px;
            color: var(--Primary-Black, #000);
            transition: 0.3s ease-in-out;
            &.custom {
              color: #fff;
              background: linear-gradient(194deg, #01231f 54.86%, #ff3d00 90.11%);
              @include response(1200px) {
                padding-left: 16px;
                margin-left: -16px;
                margin-right: -16px;
                border-radius: 0;
              }
              &::before {
                content: "";
                position: absolute;
                inset: 0;
                background: #ff3d00;
                opacity: 0;
                pointer-events: none;
                border-radius: inherit;
                z-index: 1;
                transition: opacity 320ms ease;
              }
              > * {
                position: relative;
                z-index: 1;
              }
              @include desktop-hover() {
                color: #fff;
                &::before {
                  opacity: 1;
                }
              }
            }
            @include response(1200px) {
              background: #fff;
              height: auto;
              padding: 12px 0;
            }
            @include desktop-hover() {
              color: $red;
              .nav-decor {
                opacity: 0;
              }
              .nav-decor-red {
                opacity: 1;
              }
            }
            &.coming-soon {
              pointer-events: none;
              @include response(1200px) {
                display: flex;
                column-gap: 12px;
                align-items: center;
              }
              span:not(.coming-soon-label) {
                color: #595959;
              }
            }
            .coming-soon-label {
              position: absolute;
              bottom: 28px;
              left: 50%;
              border-radius: 44px;
              background: rgba(8, 88, 78, 0.5);
              backdrop-filter: blur(2px);
              padding: 2px 8px;
              color: var(--Primary-White, #fff);
              font-size: 14px;
              font-weight: 500;
              line-height: 20px;
              transform: translateX(-50%);
              z-index: 2;
              @include response(1200px) {
                position: relative;
                left: auto;
                transform: none;
                bottom: auto;
              }
            }
            span:not(.coming-soon-label) {
              display: block;
              text-align: center;
              font-size: 16px;
              font-weight: 500;
              line-height: 24px;
              @include response(1200px) {
                font-size: 18px;
                line-height: 26px;
                text-align: left;
              }
            }
            .nav-decor {
              position: absolute;
              bottom: 0;
              right: 0;
              transition: 0.3s ease-in-out;
              @include response(1200px) {
                display: none;
              }
            }
            .nav-decor-red {
              position: absolute;
              bottom: 0;
              right: 0;
              opacity: 0;
              transition: 0.3s ease-in-out;
              @include response(1200px) {
                display: none;
              }
            }
            .nav-img {
              position: absolute;
              bottom: 0;
              right: 0;
              z-index: 1;
              @include response(1200px) {
                display: none;
              }
            }
          }
          > .custom-link {
            display: block;
            text-decoration: none;
            color: var(--Primary-Black, #000);
            text-align: center;
            @include response(1200px) {
              padding: 12px 0;
            }
            @include desktop-hover() {
              .custom-link__img {
                border-color: $red;
              }
            }
            .custom-link__img {
              display: flex;
              align-items: center;
              justify-content: center;
              border-radius: 8px;
              border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
              background: var(--Primary-White, #fff);
              height: 104px;
              margin-bottom: 8px;
              transition: 0.3s ease-in-out;
              @include response(1200px) {
                display: none;
              }
            }
            .custom-link__text {
              display: block;
              font-size: 16px;
              font-style: normal;
              font-weight: 500;
              line-height: 24px;
              @include response(1200px) {
                font-size: 18px;
                line-height: 26px;
                text-align: left;
              }
            }
          }
        }
      }
    }
    &--v2 {
      > .container {
        ul {
          column-gap: 20px;
          > li {
            width: 196px;
            flex: 0 0 196px;
            @include response(1200px) {
              width: 100%;
            }
          }
        }
      }
    }
  }
  &__nav-level-all {
    text-align: center;
    @include response(1200px) {
      display: none;
    }
  }
}

.back-level-btn {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  svg {
    transform: none;
  }
}
