.notes {
  position: relative;
  padding-bottom: 48px;
  &__decor {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -60%);
  }
  &__title {
    position: relative;
    margin-bottom: 32px;
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    @include response(1200px) {
      font-size: 22px;
      line-height: 30px;
      margin-bottom: 24px;
    }
  }
  &__slider-box {
    position: relative;
  }
  &__slider {
    position: relative;
  }
  &__slider-item {
    position: relative;
    border-radius: 48px;
    border: 1px solid var(--Line-Light, #eaeaea);
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(8px);
    padding: 32px 32px 70px;
    text-align: center;
    opacity: 0;
    overflow: hidden;
    transition: 0.2s ease-in-out;
    @include response(1200px) {
      border-radius: 32px;
      padding-bottom: 90px;
      opacity: 1;
      border: 0;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: linear-gradient(194deg, #01231f 54.86%, #ff3d00 90.11%);
      opacity: 0;
      transition: 0.4s ease-in-out;
      @include response(1200px) {
        opacity: 1;
      }
    }
    .swiper-slide-visible & {
      opacity: 1;
    }
    .swiper-slide-active & {
      color: #fff;
      &::before {
        opacity: 1;
      }
      .notes__slider-item-position {
        color: #fff;
      }
    }
  }
  &__slider-item-img {
    position: relative;
    width: 128px;
    height: 128px;
    margin: 0 auto 8px;
    border-radius: 50%;
    overflow: hidden;
    @include response(1200px) {
      width: 100px;
      height: 100px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__slider-item-name {
    position: relative;
    margin-bottom: 4px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__slider-item-position {
    position: relative;
    margin-bottom: 24px;
    line-height: 20px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #595959;
  }
  &__slider-item-text {
    position: relative;
    overflow: hidden;
    font-size: 18px;
    font-style: italic;
    line-height: 24px;
    max-height: 48px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    display: box;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    transition: 0.3s ease-in-out;
  }
  &__slider-pagination {
    display: flex;
    justify-content: center;
    padding-top: 32px;
  }
  &__slider-btn {
    position: absolute;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    top: 50%;
    margin-top: -32px;
    z-index: 1;
    @include response(1200px) {
      display: none;
    }
    svg {
      stroke: #000;
    }
    &--next {
      right: 0;
    }
    &--prev {
      left: 0;
    }
  }
}
