.types-filters {
  position: relative;
  &__holder {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    @include response(1200px) {
      display: block;
      flex: 1 1 auto;
      overflow-y: auto;
    }
  }
  &__box {
    position: relative;
  }
  &__mobile-holder {
    position: relative;
    @include response(1200px) {
      display: none;
      background: #fff;
      &.active {
        display: flex;
        flex-direction: column;
        position: fixed;
        z-index: 101;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
      }
    }
  }
  &__mobile-btn {
    display: none;
    @include response(1200px) {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 8px;
      background: var(--Primary-Main, #01231f);
      column-gap: 8px;
      padding: 8px 16px;
      color: var(--Primary-White, #fff);
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      svg {
        flex: 0 0 auto;
      }
      &--fixed {
        position: fixed;
        left: 50vw;
        transform: translate(-50%, 10px);
        bottom: 10px;
        opacity: 0;
        visibility: hidden;
        transition: .3s ease-in-out;
        &.visible {
          opacity: 1;
          visibility: visible;
          transform: translate(-50%, 0);
        }
      }
    }
  }
  &__mobile-btn-count {
    position: relative;
    flex: 0 0 20px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: $red;
    color: #fff;
    text-align: center;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__item {
    position: relative;
    flex: 0 0 calc(25% - 12px);
    width: calc(25% - 12px);
    @include response(1200px) {
      width: 100%;
      &.hide {
        display: none;
      }
    }
    @include desktop-hover() {
      .types-filters__item-front {
        border-color: #01231f;
      }
    }
  }
  &__item-front {
    position: relative;
    cursor: pointer;
    border-radius: 8px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    padding: 11px 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 8px;
    @include response-min(1201px) {
      transition: 0.3s ease-in-out;
    }
    @include response(1200px) {
      border-radius: 0;
      border: 0;
      border-bottom: 1px solid #eaeaea;
      padding: 16px;
    }
    .types-filters__item.active & {
      @include response(1200px) {
        border-bottom-color: transparent;
      }
      @include response-min(1201px) {
        border-color: #01231f;
        svg {
          transform: scaleY(-1);
        }
      }
      @include response(1200px) {
        svg {
          display: none;
        }
      }
    }
    svg {
      flex: 0 0 auto;
      stroke: #000;
      transition: 0.3s ease-in-out;
      @include response(1200px) {
        transform: rotate(-90deg);
      }
    }
  }
  &__item-title {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    @include response(1200px) {
      font-size: 18px;
      line-height: 26px;
    }
  }
  &__item-title-mobile-checked {
    display: none;
    @include response(1200px) {
      display: block;
      color: var(--Main-Gray, #595959);
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      .types-filters__item.active & {
        display: none;
      }
      &.hide {
        display: none;
      }
    }
  }
  &__item-count {
    position: relative;
    flex: 0 0 14px;
    width: 14px;
    height: 14px;
    background: #ff3d00;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--Primary-White, #fff);
    text-align: center;
    font-size: 11px;
    font-weight: 500;
    line-height: 14px;
    margin-left: auto;
    border-radius: 50%;
    &.hide {
      display: none;
    }
    @include response(1200px) {
      flex: 0 0 20px;
      width: 20px;
      height: 20px;
      background: #ffede7;
      color: #000;
      .types-filters__item.active & {
        background: $red;
        color: #fff;
      }
    }
  }
  &__item-dropdown {
    position: absolute;
    top: calc(100% + 2px);
    left: 0;
    width: 100%;
    border-radius: 8px;
    padding: 2px 13px 0 16px;
    background: var(--Primary-White, #fff);
    z-index: 1;
    @include response-min(1201px) {
      box-shadow: 0px 12px 40px 0px rgba(0, 0, 0, 0.1);
      opacity: 0;
      visibility: hidden;
      transform: translateY(10px);
      transition: 0.3s ease-in-out;
    }
    @include response(1200px) {
      display: none;
      padding-top: 0;
    }
    .active.types-filters__item & {
      @include response-min(1201px) {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
      }
      @include response(1200px) {
        display: block;
      }
    }
  }
  &__item-list {
    position: relative;
    max-height: 260px;
    overflow-y: auto;
    padding-right: 3px;
    @include response(1200px) {
      max-height: none;
    }
    &::-webkit-scrollbar {
      display: block;
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #eaeaea;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #01231f;
      border-radius: 4px;
    }
  }
  &__checkbox {
    position: relative;
    color: var(--Primary-Black, #000);
    line-height: 20px;
    padding-top: 6px;
    padding-bottom: 6px;
    padding-left: 28px;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    &.active {
      &::before {
        border-color: $red;
        background: $red;
      }
    }
    @include desktop-hover() {
      color: $red;
      .types-filters__checkbox-text {
        color: $red;
      }
    }
    &::before {
      content: "";
      position: absolute;
      width: 20px;
      height: 20px;
      left: 0;
      top: 6px;
      border-radius: 4px;
      border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
      background: var(--Primary-White, #fff);
      transition: 0.3s ease-in-out;
    }
    &::after {
      content: "";
      position: absolute;
      left: 6px;
      top: 12px;
      width: 9px;
      height: 6px;
      border-bottom: 1px solid #fff;
      border-left: 1px solid #fff;
      transform: rotate(-45deg);
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__checkbox-title {
    position: relative;
    display: flex;
    align-items: flex-start;
    column-gap: 8px;
  }
  &__checkbox-text {
    color: var(--main-50, #8f8f8f);
    font-size: 14px;
    font-weight: 400;
    transition: 0.3s ease-in-out;
  }
  &__item-controls {
    position: relative;
    margin-right: -13px;
    margin-left: -16px;
    display: flex;
    column-gap: 8px;
    padding: 12px 16px;
    border-top: 1px solid var(--Line-Light, #eaeaea);
    @include response(1200px) {
      display: none;
    }
  }
  &__item-reset {
    position: relative;
    width: 100%;
    border-radius: 8px;
    padding: 3px 12px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    color: var(--Primary-Black, #000);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    text-align: center;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: $red;
    }
  }
  &__item-apply {
    position: relative;
    width: 100%;
    border-radius: 8px;
    padding: 3px 12px;
    border: 1px solid $red;
    background: $red;
    color: var(--Primary-White, #fff);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    text-align: center;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    &.disabled {
      background: #eee;
      color: #787878;
      border-color: #eee;
    }
    @include desktop-hover() {
      background: #fff;
      color: $red;
    }
  }
  &__mobile-head {
    display: none;
    @include response(1200px) {
      position: relative;
      border-bottom: 1px solid var(--Line-Light, #eaeaea);
      background: var(--Bg-Light-gray, #f5f5f5);
      display: flex;
      align-items: center;
      column-gap: 16px;
      padding: 16px;
      flex: 0 0 auto;
      &.level {
        background: #fff;
      }
    }
  }
  &__mobile-head-back {
    display: flex;
    .types-filters__mobile-head.level & {
      svg {
        &:nth-child(1) {
          display: none;
        }
        &:nth-child(2) {
          display: block;
        }
      }
    }
    svg {
      &:nth-child(2) {
        display: none;
      }
    }
  }
  &__mobile-head-title {
    font-size: 22px;
    font-weight: 500;
    line-height: 30px;
    display: flex;
    align-items: center;
    column-gap: 8px;
  }
  &__mobile-head-count {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background: $red;
    color: var(--Primary-White, #fff);
    text-align: center;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    width: 20px;
    height: 20px;
    flex: 0 0 20px;
  }
  &__mobile-head-reset {
    position: relative;
    margin-left: auto;
    color: var(--Primary-Accent, #ff3d00);
    font-size: 16px;
    line-height: 24px;
  }
  &__mobile-controls {
    display: none;
    @include response(1200px) {
      display: block;
      flex: 0 0 auto;
      position: relative;
      padding: 12px;
      border-top: 1px solid var(--Line-Light, #eaeaea);
      background: var(--Primary-White, #fff);
    }
  }
  &__mobile-controls-btn {
    position: relative;
    text-align: center;
    border-radius: 8px;
    background: var(--Primary-Accent, #ff3d00);
    padding: 8px 32px;
    color: var(--Primary-White, #fff);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    &.disabled {
      background: #eee;
      color: #787878;
    }
    &.apply {
      .types-filters__mobile-controls.level & {
        display: none;
      }
    }
    &.save {
      display: none;
      .types-filters__mobile-controls.level & {
        display: block;
      }
    }
  }
}
