.engraving {
  position: relative;
  margin-bottom: 24px;
  border-radius: 8px;
  border: 1px solid var(--Line-Light, #eaeaea);
  background: var(--Bg-Light-gray, #f5f5f5);
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px;
    column-gap: 8px;
  }
  &__head-left {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
  }
  &__checkbox {
    position: relative;
    cursor: pointer;
    span {
      display: block;
      position: relative;
      padding-left: 36px;
      color: var(--Primary-Black, #000);
      font-size: 16px;
      font-weight: 500;
      line-height: 20px;
      @include response(1200px) {
        padding-left: 30px;
      }
      @include response(374px) {
        font-size: 14px;
      }
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        border-radius: 4px;
        border: 1px solid var(--lines-light-gray-input, #cdcdcd);
        background: var(--background-bg-white-pages, #fff);
        width: 20px;
        height: 20px;
        transition: 0.3s ease-in-out;
      }
      &::after {
        content: "";
        position: absolute;
        left: 6px;
        top: 6px;
        width: 9px;
        height: 6px;
        border-bottom: 1px solid #fff;
        border-left: 1px solid #fff;
        transform: rotate(-45deg);
      }
    }
    input {
      display: none;
      &:checked {
        & + span {
          &::before {
            background: $red;
            border-color: $red;
          }
        }
      }
    }
  }
  &__label {
    color: var(--White, #fff);
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    border-radius: 4px;
    background: var(--Secondary-Approval-Green, #0d9a88);
    padding: 2px 8px;
    text-transform: uppercase;
  }
  &__price {
    color: var(--Primary-Main, #01231f);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    flex: 0 0 auto;
    transition: 0.3s ease-in-out;
    .engraving.active & {
      color: $red;
    }
  }
  &__body {
    position: relative;
    padding: 0 16px 16px;
  }
  &__nav {
    position: relative;
    display: inline-flex;
    vertical-align: top;
    border-radius: 8px;
    background: #e9e9e9;
    padding: 4px;
    column-gap: 8px;
    margin-bottom: 12px;
  }
  &__nav-radio {
    position: relative;
    input {
      display: none;
      &:checked {
        & + span {
          border-radius: 8px;
          background: var(--Primary-White, #fff);
          box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.04);
          color: var(--Primary-Black, #000);
          svg {
            fill: #000;
          }
        }
      }
    }
    span {
      position: relative;
      display: flex;
      align-items: center;
      column-gap: 8px;
      padding: 4px 12px;
      transition: 0.3s ease-in-out;
      cursor: pointer;
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      color: var(--main-50, #8f8f8f);
      svg {
        fill: #8f8f8f;
        transition: 0.3s ease-in-out;
      }
    }
  }
  &__options {
    position: relative;
  }
  &__option {
    position: relative;
    .input-style__input {
      padding-right: 40px;
    }
    .info {
      position: absolute;
      right: 16px;
      top: 17px;
    }
  }
  &__option-text {
    color: var(--Main-Gray, #595959);
    line-height: 20px;
    margin-top: -12px;
  }
}
