.video {
  position: relative;
  padding-top: 64px;
  padding-bottom: 64px;
  background: #f5f5f5;
  overflow: hidden;
  &::before {
    content: "";
    position: absolute;
    width: 240px;
    top: 0;
    bottom: 0;
    right: 0;
    background: linear-gradient(270deg, #f5f5f5 0%, rgba(245, 245, 245, 0) 100%);
    z-index: 2;
    @include response(1200px) {
      display: none;
    }
  }
  &::after {
    content: "";
    position: absolute;
    width: 240px;
    top: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(270deg, #f5f5f5 0%, rgba(245, 245, 245, 0) 100%);
    z-index: 2;
    transform: scaleX(-1);
    @include response(1200px) {
      display: none;
    }
  }
  &--white {
    background: #fff;
    &::before {
      display: none;
    }
    &::after {
      display: none;
    }
  }
  &__ovh {
    overflow: hidden;
  }
  &__title {
    margin-bottom: 32px;
    text-align: center;
  }
  &__home-btn {
    position: absolute;
    top: 320px;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 1;
    background: #fff;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      display: none;
    }
    @include desktop-hover() {
      svg {
        stroke: $red;
      }
    }
    &--prev {
      left: 24px;
    }
    &--next {
      right: 24px;
    }
    &.swiper-button-lock {
      display: none;
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
    &.swiper-button-disabled {
      pointer-events: none;
      opacity: 0;
      visibility: hidden;
    }
  }
  &__slider-box {
    position: relative;
    max-width: 900px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    @include response(1200px) {
      max-width: 260px;
    }
    &--home {
      max-width: 848px;
      @include response(1200px) {
        max-width: 272px;
      }
    }
  }
  &__slider {
    &.swiper {
      overflow: visible;
    }
  }
  &__slider-item {
    position: relative;
  }
  &__slider-footer {
    position: relative;
    padding-top: 8px;
  }
  &__slider-name {
    position: relative;
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    margin-bottom: 2px;
  }
  &__slider-position {
    position: relative;
    text-align: center;
    line-height: 20px;
    margin-bottom: 4px;
  }
  &__slider-img {
    position: relative;
    text-align: center;
  }
  &__pagination-home,
  &__pagination {
    display: flex;
    justify-content: center;
    padding-top: 32px;
  }
  &__slider-product {
    position: relative;
    margin-top: 12px;
    border-radius: 12px;
    border: 1px solid var(--Line-Light, #eaeaea);
    background: var(--Primary-White, #fff);
    display: flex;
    align-items: center;
    column-gap: 12px;
    overflow: hidden;
  }
  &__slider-product-img {
    flex: 0 0 70px;
    width: 70px;
    height: 68px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding: 4px;
  }
  &__slider-product-box {
    position: relative;
    padding: 3px 12px;
  }
  &__slider-product-title {
    display: block;
    margin-bottom: 2px;
    color: var(--Primary-Black, #000);
    line-height: 20px;
    max-height: 40px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    display: box;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    text-decoration: none;
    transition: .3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
  }
  &__slider-product-price {
    position: relative;
    font-weight: 500;
    line-height: 20px;
  }
}

.video-item {
  position: relative;
  border-radius: 20px;
  border: 1px solid var(--Line-Light, #eaeaea);
  background: var(--Primary-White, #fff);
  padding: 6px;
  width: 260px;
  height: 420px;
  &:not(.js-hover-video) {
    cursor: pointer;
  }
  @include desktop-hover() {
    &.is-playing {
      .video-item__pause {
        opacity: 1;
      }
    }
  }
  .video__slider-box--home & {
    width: 272px;
    height: 438px;
    padding: 0;
    border: 0;
  }
  &__source {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 12px;
    @include response(1200px) {
      &:not(.js-video-item-source) {
        display: none;
      }
    }
  }
  &__btns {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    pointer-events: none;
    .js-hover-video & {
      pointer-events: initial;
    }
  }
  &__controls {
    position: absolute;
    bottom: 6px;
    left: 6px;
    right: 6px;
    border-radius: 0 0 12px 12px;
    background: var(--Primary-Main, #01231f);
    display: flex;
    align-items: center;
    column-gap: 10px;
    padding: 10px;
    cursor: auto;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s ease-in-out;
    .is-playing & {
      opacity: 1;
      visibility: visible;
    }
  }
  &__controls-play {
    position: relative;
    display: flex;
    column-gap: 4px;
    flex: 0 0 auto;
    cursor: pointer;
    &::before {
      content: "";
      display: block;
      width: 5px;
      height: 15px;
      border-radius: 1px;
      background: #fff;
    }
    &::after {
      content: "";
      display: block;
      width: 5px;
      height: 15px;
      border-radius: 1px;
      background: #fff;
    }
  }
  &__controls-time {
    position: relative;
    flex: 0 0 auto;
    color: #fff;
    font-size: 12px;
    line-height: 24px;
  }
  &__controls-progress {
    position: relative;
    flex: 1 1 auto;
    height: 6px;
    border-radius: 6px;
    background: rgba(255, 255, 255, 0.24);
    overflow: hidden;
    cursor: pointer;
    &::before {
      content: "";
      display: block;
      height: 100%;
      background: #0d9a88;
      width: var(--progress, 0%);
      transition: width 0.1s ease;
    }
  }
  &__controls-length {
    flex: 0 0 auto;
    position: relative;
    color: #fff;
    font-size: 12px;
    line-height: 24px;
  }
  &__controls-mute {
    position: relative;
    flex: 0 0 auto;
    cursor: pointer;
    display: flex;
    &::before {
      content: "";
      position: absolute;
      width: 2px;
      height: 20px;
      left: 50%;
      margin-left: -1px;
      top: 50%;
      margin-top: -10px;
      transform: rotate(45deg);
      transform-origin: center center;
      background: #fff;
      opacity: 0;
      transition: 0.1s ease-in-out;
    }
    &.is-muted {
      &::before {
        opacity: 1;
      }
    }
  }
  &__play {
    position: relative;
    width: 64px;
    height: 64px;
    border-radius: 70px;
    border: 1px solid rgba(255, 255, 255, 0.16);
    background: rgba(8, 88, 78, 0.6);
    backdrop-filter: blur(2px);
    cursor: pointer;
    transition: 0.1s ease-in-out;
    .is-playing & {
      opacity: 0;
    }
    &::before {
      content: "";
      position: absolute;
      border: 8px solid transparent;
      border-left: 12px solid #fff;
      top: 50%;
      left: 50%;
      margin-top: -8px;
      margin-left: -4px;
    }
  }
  &__pause {
    position: absolute;
    top: 50%;
    left: 50%;
    display: flex;
    column-gap: 4px;
    margin-left: -12px;
    margin-top: -14px;
    opacity: 0;
    transition: 0.1s ease-in-out;
    &::before {
      content: "";
      display: block;
      width: 10px;
      height: 28px;
      border-radius: 2px;
      background: #fff;
    }
    &::after {
      content: "";
      display: block;
      width: 10px;
      height: 28px;
      border-radius: 2px;
      background: #fff;
    }
  }
}
