.links-category {
  position: relative;
  border-radius: 8px;
  border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
  background: var(--Bg-Light-gray, #f5f5f5);
  padding: 16px;
  &__title {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 10px;
  }
  &__main {
    position: relative;
    margin-bottom: 32px;
    display: flex;
    align-items: center;
    column-gap: 8px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    @include response(1200px) {
      margin-bottom: 24px;
    }
  }
  &__name {
    color: var(--Primary-Accent, #ff3d00);
    font-family: "Instrument Sans";
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
    text-decoration: none;
    border-bottom: 1px solid #ff3d00;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $redH;
      border-bottom-color: $redH;
    }
  }
  &__list {
    position: relative;
  }
  &__item {
    display: block;
    text-decoration: none;
    padding: 8px;
    position: relative;
    margin-bottom: 12px;
    border-radius: 8px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    display: flex;
    align-items: center;
    column-gap: 12px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: #ff3d00;
      .links-category__item-title {
        color: #ff3d00;
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__item-img {
    position: relative;
    flex: 0 0 60px;
    width: 60px;
    height: 60px;
    border-radius: 4px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-box {
    position: relative;
  }
  &__item-title {
    position: relative;
    margin-bottom: 4px;
    color: var(--Primary-Black, #000);
    font-size: 16px;
    line-height: 24px;
    transition: .3s ease-in-out;
  }
  &__item-price-box {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 4px;
  }
  &__item-price {
    color: var(--Primary-Black, #000);
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
  }
  &__item-price-new {
    color: var(--Secondary-Red, #f70808);
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
  }
  &__item-price-old {
    color: var(--main-50, #8f8f8f);
    font-size: 12px;
    font-weight: 500;
    line-height: normal;
    text-decoration-line: line-through;
  }
}
