.builder {
  position: relative;
  @include response(1200px) {
    padding-bottom: 60px;
  }
  &--last {
    @include response(1200px) {
      padding-bottom: 0;
    }
  }
  &__header {
    position: relative;
    display: flex;
    column-gap: 20px;
    padding-bottom: 16px;
    @include response(1200px) {
      column-gap: 12px;
    }
  }
  &__logo {
    position: relative;
    flex: 0 0 100px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    @include response(1200px) {
      flex: 0 0 48px;
      width: 48px;
      height: 48px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__header-box {
    position: relative;
    padding-top: 16px;
    flex: 1 1 auto;
    overflow: auto;
    @include response(1200px) {
      padding-top: 0;
      overflow: visible;
    }
  }
  &__header-holder {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 12px;
    @include response(1200px) {
      align-items: flex-start;
    }
  }
  &__title-rating {
    position: relative;
  }
  &__title {
    position: relative;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    margin: 0 0 4px;
    @include response(1200px) {
      font-size: 16px;
      line-height: 24px;
    }
  }
  &__rating {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
  }
  &__rating-value {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__reviews-link {
    position: relative;
    color: var(--Primary-Accent, #ff3d00);
    line-height: 20px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $redH;
    }
  }
  &__reviews-dancer-btn {
    flex: 0 0 auto;
    @include response(1200px) {
      display: none;
    }
  }
  &__toggle-info-mobile {
    display: none;
    @include response(1200px) {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 8px;
      border: 1px solid $red;
      width: 32px;
      height: 32px;
      flex: 0 0 32px;
      svg {
        fill: $red;
      }
    }
  }
  &__toggle-info {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    background: transparent;
    border: 0;
    padding: 0;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      display: none;
    }
    @include desktop-hover() {
      color: $red;
      svg {
        stroke: $red;
      }
    }
    > span {
      &:nth-child(1) {
      }
      &:nth-child(2) {
        display: none;
      }
    }
    .builder__header-box.active & {
      > span {
        &:nth-child(1) {
          display: none;
        }
        &:nth-child(2) {
          display: block;
        }
      }
    }
  }
  &__toggle-icon {
    position: relative;
    display: flex;
    transition: 0.3s ease-in-out;
    .builder__header-box.active & {
      transform: scaleY(-1);
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
  }
  &__header-info {
    margin-top: 16px;
    position: relative;
    // @include response(1200px) {
    //   display: none;
    // }
    @include response(1200px) {
      margin-left: -60px;
    }
  }
  &__details {
    position: relative;
    padding-bottom: 16px;
    padding-top: 16px;
    border-top: 1px solid #eaeaea;
    border-bottom: 1px solid #eaeaea;
    display: flex;
    column-gap: 32px;
    margin-bottom: 16px;
    @include response(1200px) {
      display: block;
      padding: 0 16px;
      margin-bottom: 12px;
      .builder__header-box & {
        margin-bottom: 0;
        border-bottom: 0;
      }
    }
  }
  &__detail-item {
    position: relative;
    flex: 0 0 272px;
    width: 272px;
    display: flex;
    column-gap: 8px;
    @include response(1200px) {
      width: 100%;
      padding-top: 12px;
      padding-bottom: 12px;
      border-bottom: 1px solid #eaeaea;
      &:last-child {
        border-bottom: 0;
      }
    }
    &::before {
      content: "";
      position: absolute;
      width: 1px;
      height: 100%;
      top: 0;
      background: #eaeaea;
      right: -16px;
      @include response(1200px) {
        display: none;
      }
    }
    &:last-child {
      &::before {
        display: none;
      }
    }
  }
  &__detail-icon {
    position: relative;
    flex: 0 0 20px;
    display: flex;
  }
  &__detail-content {
    position: relative;
  }
  &__detail-label {
    display: block;
    margin-bottom: 4px;
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
  }
  &__detail-value {
    position: relative;
    display: block;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__description {
    position: relative;
    @include response(1200px) {
      display: none;
    }
  }
  &__text {
    margin-bottom: 10px;
    max-height: 48px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    display: box;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    max-width: 880px;
    transition: 0.3s ease-in-out;
    .builder__description.active & {
      max-height: none;
      -webkit-line-clamp: initial;
    }
    @include response(1200px) {
      max-height: none;
      -webkit-line-clamp: initial;
      &.text-content {
        font-size: 16px;
      }
    }
  }
  &__read-more {
    position: relative;
    display: inline-flex;
    align-items: center;
    column-gap: 12px;
    text-decoration: underline;
    padding: 0;
    background: transparent;
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    border: 0;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
      svg {
        stroke: $red;
      }
    }
    svg {
      flex: 0 0 auto;
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
  }
  &__read-more-icon {
    display: flex;
    transition: 0.3s ease-in-out;
    .builder__description.active & {
      transform: scaleY(-1);
    }
  }
  &__body-box {
    position: relative;
    display: flex;
    column-gap: 40px;
    align-items: flex-start;
    @include response(1200px) {
      display: block;
    }
  }
  &__body {
    position: relative;
    border-top: 1px solid #eaeaea;
    padding-top: 40px;
    @include response(1200px) {
      padding-top: 16px;
    }
    &--without-border {
      border-top: 0;
      @include response(1200px) {
        padding-top: 0;
      }
    }
  }
  &__aside {
    position: sticky;
    top: 10px;
    width: 290px;
    flex: 0 0 290px;
    background: #fff;
    @include response(1200px) {
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 2;
      width: 100vw;
      display: none;
      &.active {
        display: block;
      }
    }
  }
  &__main {
    position: relative;
    flex: 1 1 auto;
  }
  &__main-head {
    position: relative;
    margin-bottom: 20px;
    @include response(1200px) {
      margin-bottom: 16px;
      padding-bottom: 16px;
      &::after {
        content: "";
        position: absolute;
        background: #eaeaea;
        height: 1px;
        left: -16px;
        right: -16px;
        bottom: 0;
      }
    }
  }
  &__main-counter-steps {
    position: relative;
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    column-gap: 16px;
    line-height: 20px;
  }
  &__main-counter-steps-title {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 1px;
      height: 20px;
      background: #d3d3d3;
      top: 0;
      right: -8px;
    }
  }
  &__main-counter-steps-val {
    font-weight: 500;
    span {
      opacity: 0.5;
    }
  }
  &__main-title {
    margin-bottom: 8px;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
  }
  &__main-text {
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 24px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__main-attention {
    position: relative;
    display: flex;
    border-left: 2px solid var(--Colors-Orange, #ff9500);
    background: var(--Secondary-Light-yellow, #f9f3eb);
    padding: 8px 12px;
    column-gap: 8px;
    font-size: 16px;
    line-height: 20px;
    svg {
      flex: 0 0 auto;
    }
    span {
      font-weight: 500;
    }
  }
  &__list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 20px;
    @include response(1023px) {
      gap: 12px 16px;
    }
  }
  &__item {
    position: relative;
    flex: 0 0 calc(25% - 15px);
    width: calc(25% - 15px);
    border-radius: 2px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: border-color 0.3s ease-in-out;
    @include response(1023px) {
      flex: 0 0 calc(50% - 8px);
      width: calc(50% - 8px);
    }
    &.active {
      border-color: $red;
    }
    &.disabled {
      pointer-events: none;
      border-color: #eaeaea;
    }
  }
  &__item-label {
    position: relative;
    background: #d3d3d3;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 4px;
    padding: 2px 8px;
    color: var(--Primary-White, #fff);
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    .disabled & {
      color: #8f8f8f;
    }
  }
  &__item-gallery {
    position: relative;
    cursor: pointer;
    .disabled & {
      opacity: 0.5;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-gallery-icon {
    position: absolute;
    bottom: 12px;
    right: 12px;
    border-radius: 50%;
    width: 28px;
    height: 28px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Bg-Light-gray, #f5f5f5);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: $red;
      border-color: $red;
      svg {
        fill: #fff;
      }
    }
    @include response(1200px) {
      bottom: 7px;
      right: 7px;
    }
    svg {
      transition: 0.3s ease-in-out;
    }
    .number-imgs {
      position: absolute;
      left: 8px;
      top: 7px;
      width: 12px;
      text-align: center;
      font-size: 10px;
      font-weight: 700;
    }
  }
  &__item-box {
    position: relative;
    border-top: 1px solid #eaeaea;
    padding-left: 12px;
    padding-right: 12px;
    .disabled & {
      opacity: 0.5;
    }
  }
  &__item-select {
    padding-top: 12px;
    padding-bottom: 12px;
    border-bottom: 1px solid #eaeaea;
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 4px;
  }
  &__item-checkbox {
    position: relative;
    cursor: pointer;
    overflow: auto;
    input {
      display: none;
      &:checked {
        & + span {
          &::before {
            background: $red;
            border-color: $red;
          }
        }
      }
    }
    span {
      display: block;
      color: var(--Primary-Black, #000);
      font-family: "Instrument Sans";
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      padding-left: 24px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        color: $red;
      }
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -8px;
        width: 16px;
        height: 16px;
        border-radius: 4px;
        border: 1px solid var(--lines-light-gray-input, #cdcdcd);
        background: var(--background-bg-white-pages, #fff);
        transition: 0.3s ease-in-out;
      }
      &::after {
        content: "";
        position: absolute;
        width: 8px;
        height: 4px;
        border-bottom: 1px solid #fff;
        border-left: 1px solid #fff;
        top: 50%;
        margin-top: -3px;
        left: 4px;
        transform: rotate(-45deg);
      }
    }
    .disabled & {
      span {
        &::before {
          background: #e6e6e6;
          border-color: #e6e6e6;
        }
        &::after {
          display: none;
        }
      }
    }
  }
  &__item-price {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    transition: 0.3s ease-in-out;
    flex: 0 0 auto;
    .builder__item.active & {
      color: $red;
    }
  }
  &__item-bottom {
    position: relative;
    padding-top: 12px;
    padding-bottom: 12px;
    display: flex;
    align-items: center;
    column-gap: 4px;
    justify-content: space-between;
    .info {
      flex: 0 0 auto;
    }
  }
  &__item-name {
    position: relative;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  &__informer {
    position: relative;
    border-radius: 4px;
    border-left: 2px solid var(--main-50, #8f8f8f);
    background: var(--Bg-Light-gray, #f5f5f5);
    display: flex;
    align-items: flex-start;
    column-gap: 12px;
    padding: 16px;
    svg {
      flex: 0 0 auto;
    }
  }
  &__informer-text {
    font-size: 16px;
    line-height: 24px;
    p {
      margin: 0 0 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__parts-btn {
    position: relative;
    border-radius: 8px;
    background: var(--Secondary-Approval-Green, #0d9a88);
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 12px;
    padding: 8px 12px;
    margin-bottom: 14px;
    color: var(--Primary-White, #fff);
    font-weight: 500;
    line-height: 20px;
    cursor: pointer;
    @include response(1200px) {
      display: none;
    }
  }
  &__parts-btn-mobile {
    display: none;
    @include response(1200px) {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: auto;
      border-radius: 58px;
      background: var(--main-70, #4d6562);
      column-gap: 6px;
      padding: 4px 8px 4px 4px;
      color: var(--Primary-White, #fff);
      font-style: normal;
      font-weight: 500;
      line-height: 20px;
    }
  }
  &__options-holder {
    border-radius: 8px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: #fff;
    overflow: hidden;
    @include response(1200px) {
      border: 0;
      height: 100%;
      display: flex;
      flex-direction: column;
    }
  }
  &__options {
    position: relative;
    height: 482px;
    @include response(1200px) {
      height: calc(100% - 74px);
    }
  }
  &__options-empty {
    position: relative;
    padding-top: 40px;
    text-align: center;
    svg {
      margin-bottom: 12px;
    }
  }
  &__options-empty-title {
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
  }
  &__controls {
    position: relative;
    padding: 16px 20px;
    background: #f5f5f5;
  }
  &__controls-price {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 4px;
    margin-bottom: 16px;
    @include response(1200px) {
      margin-bottom: 0;
    }
  }
  &__controls-price-title {
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__controls-price-val {
    color: var(--Primary-Main, #01231f);
    text-align: right;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__controls-btns {
    position: relative;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    column-gap: 8px;
    row-gap: 12px;
    @include response(1200px) {
      position: fixed;
      left: 20px;
      bottom: 20px;
      right: 20px;
      z-index: 1;
      justify-content: space-between;
      column-gap: 0;
    }
  }
  &__controls-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 7px 15px;
    column-gap: 8px;
    width: calc(50% - 4px);
    border-radius: 8px;
    font-family: "Instrument Sans";
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    text-decoration: none;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      width: auto;
    }
    svg {
      fill: none;
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
    &.disabled {
      pointer-events: none;
    }
    &--prev {
      color: var(--Primary-Black, #000);
      background: var(--Primary-White, #fff);
      @include response(1200px) {
        font-size: 0;
        column-gap: 0;
        height: 48px;
        order: 1;
      }
      @include desktop-hover() {
        background: $red;
        border-color: $red;
        color: #fff;
        svg {
          stroke: #fff;
        }
      }
      &.disabled {
        color: #a4a4a4;
        svg {
          stroke: #a4a4a4;
        }
      }
    }
    &--next {
      color: #fff;
      background: $red;
      border-color: $red;
      @include response(1200px) {
        padding-left: 24px;
        padding-right: 24px;
        order: 3;
      }
      @include desktop-hover() {
        background: #fff;
        color: $red;
        svg {
          stroke: $red;
        }
      }
      svg {
        stroke: #fff;
      }
      &.disabled {
        background: #d1d1d1;
        border-color: #d1d1d1;
      }
    }
    &--without-text-mobile {
      @include response(1200px) {
        font-size: 0;
        padding-left: 15px;
        padding-right: 15px;
        column-gap: 0;
      }
    }
  }
  &__controls-btn-last {
    display: flex;
    align-items: center;
    column-gap: 8px;
    color: var(--Secondary-Approval-Green, #0d9a88);
    font-family: "Instrument Sans";
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    text-decoration: none;
    @include response(1200px) {
      order: 2;
      border-radius: 8px;
      background: var(--Secondary-Approval-Green, #0d9a88);
      padding: 12px 24px;
      color: #fff;
    }
    @include desktop-hover() {
      .path-arrow {
        &:nth-child(1) {
          transform: translateX(2px);
        }
        &:nth-child(2) {
          transform: translateX(2px);
        }
      }
    }
    svg {
      stroke: #0d9a88;
      @include response(1200px) {
        stroke: #fff;
      }
      .path-arrow {
        transition: 0.3s ease-in-out;
        &:nth-child(2) {
          transition-delay: 0.1s;
        }
      }
    }
  }
  &__options-item {
    position: relative;
  }
  &__options-item-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 20px;
    gap: 12px;
    border-bottom: 1px solid var(--Line-Light, #eaeaea);
    background: var(--Bg-Light-gray, #f5f5f5);
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    cursor: pointer;
    .builder__options-item.active & {
      svg {
        transform: scaleY(1);
      }
    }
    svg {
      flex: 0 0 auto;
      stroke: #000;
      transform: scaleY(-1);
      transition: 0.3s ease-in-out;
    }
  }
  &__options-item-body {
    position: relative;
    display: none;
    overflow-y: auto;
    overscroll-behavior: contain;
    &::-webkit-scrollbar {
      display: block;
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #eaeaea;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #01231f;
      border-radius: 4px;
    }
  }
  &__options-item-row {
    position: relative;
    border-bottom: 1px solid var(--Line-Light, #eaeaea);
    padding: 8px 20px;
  }
  &__options-item-row-head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 8px;
    margin-bottom: 4px;
  }
  &__options-item-row-title {
    position: relative;
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
  }
  &__options-item-edit {
    position: relative;
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    column-gap: 8px;
    color: var(--Primary-Black, #000);
    line-height: 20px;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
      svg {
        fill: $red;
      }
    }
    svg {
      fill: #000;
      transition: 0.3s ease-in-out;
    }
  }
  &__options-item-row-body {
    position: relative;
    display: flex;
    column-gap: 8px;
    justify-content: space-between;
    margin-bottom: 4px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__options-item-row-name {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__options-item-row-value {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    flex: 0 0 auto;
  }
  &__select {
    position: relative;
    max-width: 480px;
    margin-bottom: 30px;
  }
  &__checkbox-list {
    position: relative;
    max-width: 480px;
    width: 100%;
  }
  &__checkbox-option {
    position: relative;
    margin-bottom: 16px;
    border-radius: 4px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    padding: 12px 16px;
    transition: 0.3s ease-in-out;
    &.active {
      border-color: $red;
    }
    &:last-child {
      margin-bottom: 0;
    }
    label {
      display: block;
      > input {
        display: none;
        &:checked {
          & + span {
            &::before {
              background: $red;
              border-color: $red;
            }
            .price {
              color: $red;
            }
          }
        }
      }
      > span {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        padding-left: 36px;
        color: var(--Primary-Black, #000);
        font-family: "Instrument Sans";
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        &::before {
          content: "";
          position: absolute;
          width: 24px;
          height: 24px;
          left: 0;
          top: 50%;
          margin-top: -12px;
          border-radius: 4px;
          border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
          background: var(--Primary-White, #fff);
          transition: 0.3s ease-in-out;
        }
        &::after {
          content: "";
          position: absolute;
          width: 12px;
          height: 8px;
          border-left: 2px solid #fff;
          border-bottom: 2px solid #fff;
          transform: rotate(-45deg);
          top: 6px;
          left: 6px;
        }
        .price {
          font-weight: 500;
          transition: 0.3s ease-in-out;
        }
      }
    }
    .custom-message {
      padding-top: 12px;
      padding-left: 32px;
      textarea {
        display: block;
        width: 100%;
        padding: 4px 0 12px;
        font-family: "Instrument Sans";
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        border: 0;
        field-sizing: content;
        height: 40px;
        min-height: 40px;
        resize: none;
        border-radius: 0;
        border-bottom: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
        &::placeholder {
          color: var(--main-50, #8f8f8f);
        }
      }
    }
  }
  &__accordion {
    position: relative;
  }
  &__accordion-item {
    position: relative;
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__accordion-head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 8px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    padding: 11px 23px;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: #8f8f8f;
    }
    .builder__accordion-item.active & {
      border-color: #8f8f8f;
    }
  }
  &__accordion-title {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 16px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    svg {
      flex: 0 0 auto;
      transition: 0.3s ease-in-out;
    }
    .builder__accordion-item.active & {
      svg {
        transform: scaleY(-1);
      }
    }
  }
  &__accordion-price {
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    flex: 0 0 auto;
  }
  &__accordion-body {
    display: none;
    padding-top: 16px;
  }
  &__upgrades {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 20px 24px;
    @include response(767px) {
      display: block;
    }
  }
  &__upgrades-item {
    position: relative;
    flex: 0 0 calc(50% - 12px);
    width: calc(50% - 12px);
    @include response(767px) {
      width: 100%;
      margin-bottom: 16px;
    }
    &.disabled {
      pointer-events: none;
    }
  }
  &__upgrades-item-box {
    position: relative;
    border-radius: 0px 8px 8px 8px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    transition: 0.3s ease-in-out;
    .builder__upgrades-item.active & {
      border-color: $red;
    }
    .builder__upgrades-item.disabled & {
      background: #f8f8f8;
    }
  }
  &__upgrades-label {
    position: relative;
    display: inline-flex;
    align-items: center;
    border-radius: 8px 12px 0px 0px;
    background: var(--main-6, #f1f1f1);
    padding: 4px 12px 4px 8px;
    column-gap: 8px;
    font-weight: 500;
    line-height: 20px;
    transition: 0.3s ease-in-out;
    .builder__upgrades-item.active & {
      background: $red;
      color: #fff;
      svg {
        stroke: #fff;
      }
    }
    svg {
      stroke: #8f8f8f;
      transition: 0.3s ease-in-out;
    }
  }
  &__upgrades-head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid var(--Line-Light, #eaeaea);
    padding: 16px 12px;
  }
  &__upgrades-input {
    position: relative;
    cursor: pointer;
    input {
      display: none;
      &:checked {
        & + span {
          &::before {
            background: #ff3d00;
          }
          &::after {
            transform: translateX(16px);
          }
        }
      }
    }
    span {
      display: block;
      position: relative;
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      padding-left: 48px;
      @include response(374px) {
        font-size: 14px;
      }
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 2px;
        width: 36px;
        height: 20px;
        border-radius: 9999px;
        background: var(--main-10, #e6e9e9);
        transition: 0.3s ease-in-out;
      }
      &::after {
        content: "";
        position: absolute;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background: #fff;
        left: 2px;
        top: 4px;
        transition: 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
      }
    }
    .builder__upgrades-item.disabled & {
      span {
        color: #8f8f8f;
      }
    }
  }
  &__upgrades-price {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    flex: 0 0 auto;
    transition: 0.3s ease-in-out;
    @include response(374px) {
      font-size: 14px;
    }
    .builder__upgrades-item.active & {
      color: $red;
    }
    .builder__upgrades-item.disabled & {
      color: #8f8f8f;
    }
  }
  &__upgrades-text {
    padding: 16px 12px;
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
    .builder__upgrades-item.disabled & {
      color: #8f8f8f;
    }
  }
  &__upgrades-info {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    padding: 4px 12px;
    color: var(--Primary-Main, #01231f);
    line-height: 155%;
    border-radius: 0px 0px 8px 8px;
    border-top: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Secondary-Light-yellow, #f9f3eb);
    svg {
      flex: 0 0 auto;
    }
  }
  &__upgrades-row {
    position: relative;
    padding: 8px 12px;
    display: flex;
    column-gap: 10px;
    color: #595959;
    font-size: 16px;
    line-height: 24px;
    border-bottom: 1px solid var(--Line-Light, #eaeaea);
    span {
      color: $red;
      font-weight: 500;
    }
    .builder__upgrades-item.disabled & {
      color: rgba(#595959, 0.5);
      span {
        opacity: 0.5;
      }
    }
  }
  &__order-title {
    position: relative;
    text-align: center;
    margin-bottom: 12px;
    padding-top: 40px;
    @include response(1200px) {
      padding-top: 32px;
    }
  }
  &__order-description {
    color: var(--Primary-Main, #01231f);
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    margin: 0 0 32px;
    p {
      margin: 0;
    }
    @include response(1200px) {
      color: var(--Main-Gray, #595959);
    }
  }
  &__order-box {
    position: relative;
    border: 1px solid var(--Line-Light, #eaeaea);
    display: flex;
    @include response(1200px) {
      display: block;
      border: 0;
    }
  }
  &__order-cell {
    position: relative;
    width: 33.333%;
    flex: 0 0 33.333%;
    border-right: 1px solid var(--Line-Light, #eaeaea);
    display: flex;
    flex-direction: column;
    @include response(1200px) {
      display: block;
      width: 100%;
      border-left: 1px solid var(--Line-Light, #eaeaea);
      &:first-child {
        border-top: 1px solid var(--Line-Light, #eaeaea);
      }
    }
    &:last-child {
      border-right: 0;
      @include response(1200px) {
        border-left: 1px solid var(--Line-Light, #eaeaea);
      }
    }
  }
  &__order-cell-title {
    position: relative;
    padding: 12px 24px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    background: #f5f5f5;
    border-bottom: 1px solid var(--Line-Light, #eaeaea);
    @include response(1200px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      column-gap: 10px;
      padding-left: 16px;
      padding-right: 16px;
    }
    svg {
      display: none;
      @include response(1200px) {
        display: block;
        transform: scaleY(1);
        transition: 0.3s ease-in-out;
      }
    }
    .builder__order-cell.active & {
      svg {
        transform: scaleY(-1);
      }
    }
  }
  &__order-body {
    position: relative;
    margin-bottom: -1px;
    flex: 1 1 auto;
    @include response(1200px) {
      .builder__order-cell.active & {
        display: none;
      }
    }
  }
  &__order-body-row {
    position: relative;
    padding: 8px 24px;
    border-bottom: 1px solid var(--Line-Light, #eaeaea);
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__order-body-title {
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 4px;
  }
  &__order-body-box {
    position: relative;
    display: flex;
    justify-content: space-between;
    column-gap: 10px;
  }
  &__order-body-text {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__order-body-price {
    position: relative;
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    column-gap: 8px;
  }
  &__order-body-price-value {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__order-form {
    position: relative;
    padding: 24px 24px 16px;
    background: #fff;
    @include response(1200px) {
      position: sticky;
      bottom: 0;
      padding: 0;
      margin: 32px -16px -20px;
      box-shadow: 0px -6px 6px 0px rgba(0, 0, 0, 0.04);
    }
  }
  &__order-form-input {
    position: relative;
    margin-bottom: 12px;
    @include response(1200px) {
      padding: 12px 16px;
      margin-bottom: 0;
      .input-style__label {
        margin-bottom: 0;
        color: var(--Primary-Black, #000);
        font-size: 16px;
        font-weight: 500;
        line-height: 24px;
      }
      textarea {
        display: none;
        margin-top: 16px;
        min-height: 5lh;
      }
    }
    .builder__order-form.active & {
      textarea {
        @include response(1200px) {
          display: block;
        }
      }
    }
  }
  &__order-form-total {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 12px;
    @include response(1200px) {
      padding: 12px 16px 8px;
      margin-bottom: 0;
      border-top: 1px solid var(--Line-Light, #eaeaea);
    }
  }
  &__order-form-total-title {
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__order-form-total-val {
    text-align: right;
    font-size: 22px;
    font-weight: 500;
    line-height: 30px;
    @include response(1200px) {
      font-size: 18px;
      line-height: 26px;
    }
  }
  &__order-form-info {
    position: relative;
    display: flex;
    padding: 8px 12px;
    justify-content: center;
    align-items: center;
    gap: 4px;
    border-radius: 4px;
    margin-bottom: 12px;
    background: var(--Bg-Light-gray, #f5f5f5);
    @include response(1200px) {
      margin-left: 16px;
      margin-right: 16px;
    }
  }
  &__order-form-info-title {
    color: var(--Main-Gray, #595959);
    line-height: 20px;
    span {
      font-weight: 500;
      color: #000;
    }
  }
  &__order-btn-box {
    text-align: center;
    .btn {
      width: 100%;
      @include response(1200px) {
        border-radius: 0;
      }
    }
  }
  &__video-list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    &--no-wrap {
      flex-wrap: nowrap;
      overflow-x: auto;
      .builder__video-item {
        flex: 0 0 123px;
        width: 123px;
      }
    }
    @include response(1200px) {
      flex-wrap: nowrap;
      overflow-x: auto;
      width: 100vw;
      margin-left: -16px;
      margin-right: -16px;
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__video-item {
    position: relative;
    display: flex;
    border-radius: 12px;
    border: 2px solid var(--main-50, #8f8f8f);
    padding: 2px;
    overflow: hidden;
    flex: 0 0 100px;
    width: 100px;
    @include response(1200px) {
    }
    @include desktop-hover() {
      .builder__video-item-btn {
        transform: scale(1);
      }
    }
  }
  &__video-source {
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 8px;
    @include response(1200px) {
      &:not(.js-video-item-source) {
        display: none;
      }
    }
  }
  &__video-title {
    position: relative;
    margin-bottom: 8px;
    overflow: hidden;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    &--fz18 {
      font-size: 18px;
    }
  }
  &__video-item-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 28px;
    height: 28px;
    margin-left: -14px;
    margin-top: -14px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.3);
    background: var(--Line-Opacity---16, rgba(255, 255, 255, 0.16));
    backdrop-filter: blur(7px);
    transform: scale(0);
    padding-left: 2px;
    cursor: pointer;
    transition: 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
    @include response(1200px) {
      transform: scale(1);
    }
  }
}
